import React, { useEffect, useState } from "react";
import designesis from "../../assets/js/designesia";
import { Link, useNavigate, useParams } from "react-router-dom";
import INews from "../../types/advanced/INews";
import FileDbService from "../../services/news/advanced/FileDbService";
import { useSelector } from "react-redux";
import { RootState } from "../../store/store";

import "react-toastify/dist/ReactToastify.css";
import { ToastContainer, toast } from "react-toastify";

function NewsSingle() {
  useEffect(() => {
    designesis();
  }, []);
  // todo : 
  //    (uuid)
  const { uuid } = useParams();

  //  ( :  )
  const initialFileDb = {
    uuid: "", // (    )
    fileTitle: "", // 
    fileContent: "", // 
    fileUrl: "", //   URL
    insertTime: "", // 
    updateTime: "", // 
  };

  // uploadFileDb 
  const [uploadFileDb, setUploadFileDb] = useState<INews>(initialFileDb);
  //  
  let navigate = useNavigate();
  //    
  const userRole = useSelector((state: RootState) => state.auth.user?.role);

  //   
  const [fileDb, setFileDb] = useState<Array<INews>>([]);
  // todo:   : page(), count(), pageSize(3,6,9 )
  const [page, setPage] = useState<number>(1);
  const [count, setCount] = useState<number>(1);
  const [pageSize, setPageSize] = useState<number>(6); // 1
  // todo:  pageSizes :  (  )
  const pageSizes = [3, 6, 9];
  // title  
  const [searchTitle, setSearchTitle] = useState<string>("");

  // todo : 
  //  
  const getFileDb = (uuid: string) => {
    FileDbService.getFileDb(uuid) //   
      .then((response: any) => {
        setUploadFileDb(response.data);
        console.log(response.data);
      })
      .catch((e: Error) => {
        console.log(e);
      });
  };

  //     + uuid   
  useEffect(() => {
    if (uuid) getFileDb(uuid);
  }, [uuid]);

  // (uuid)
  const deleteImage = (uuid: any) => {
    FileDbService.deleteFile(uuid) //  
      .then((response: any) => {
        console.log(response.data);
        alert(".");
        // 
        navigate("/news");
      })
      .catch((e: Error) => {
        console.log(e);
      });
  };

  // todo:   
  //    
  const retrieveFileDb = () => {
    //    : + (page), 1(pageSize)
    FileDbService.getFiles(searchTitle, page - 1, pageSize) //  
      .then((response: any) => {
        const { fileDb, totalPages } = response.data;
        setFileDb(fileDb);
        setCount(totalPages);
        console.log("response", response.data);
      })
      .catch((e: Error) => {
        //   
        console.log(e);
      });
  };

  useEffect(() => {
    retrieveFileDb(); //  
  }, [page, pageSize]);

  useEffect(() => {
    //          
    if (uuid && fileDb.length > 0) {
      const randomIndex = Math.floor(Math.random() * fileDb.length);
      const recommendedNews = fileDb[randomIndex];

      toast.info(
        <div>
          <div>  ?</div>
          <div style={{ whiteSpace: "pre-line" }}>
            {recommendedNews.fileTitle}
          </div>
        </div>,
        {
          position: "bottom-right",
          autoClose: 5000,
          hideProgressBar: true,
          closeOnClick: true,
          pauseOnHover: true,
          draggable: true,
        }
      );
    }
  }, [uuid, fileDb]);

  //   
  const shareOnTwitter = () => {
    //    URL  
    const tweetText = encodeURIComponent(`**playhost  .** \n [${uploadFileDb.fileTitle}]`);
    const tweetUrl = encodeURIComponent(window.location.href);
    const tweetHashtags = ",IT"; //   

    //   URL 
    const twitterUrl = `https://twitter.com/intent/tweet?text=${tweetText}&url=${tweetUrl}&hashtags=${tweetHashtags}`;

    //      
    window.open(twitterUrl, "_blank");
  };

  //   
  const shareOnFacebook = () => {
    //   
    const shareUrl = window.location.href;

    //   API   
    const facebookLink = `https://www.facebook.com/sharer/sharer.php?u=${encodeURIComponent(
      shareUrl
    )}`;

    //   
    window.open(facebookLink, "_blank");
  };

  return (
    <>
      <div className="no-bottom no-top" id="content">
        <div id="top"></div>

        <section id="subheader" className="jarallax">
          <img
            src={require("../../assets/images/background/subheader-news-default.webp")}
            className="jarallax-img"
            alt=""
          />
          <div className="container z-1000">
            <div className="row">
              <div className="col-lg-12">
                <div className="subtitle wow fadeInUp mb-3"></div>
              </div>
              <div className="col-lg-12">
                <br />
                <h3 className="wow fadeInUp mb20" data-wow-delay=".2s">
                  {uploadFileDb.fileTitle}
                  <br />
                  <br />
                  {/* TODO:     ,     */}
                  {uploadFileDb.updateTime ? (
                    <h6>
                      <div className="subtitle wow fadeInUp"></div>{" "}
                      {uploadFileDb.updateTime}
                    </h6>
                  ) : (
                    <h6>
                      <div className="subtitle wow fadeInUp"></div>{" "}
                      {uploadFileDb.insertTime}
                    </h6>
                  )}
                </h3>
              </div>
            </div>
          </div>
        </section>

        {/*   */}
        <section style={{ paddingTop: "5px", margin: "0px" }}>
          <div className="container">
            <div className="row">
              {/*   */}
              <div className="col-md-8 mt-5">
                <div className="blog-read">
                  <div className="post-text">
                    {/*  URL      */}
                    <img
                      alt=""
                      src={`${uploadFileDb.fileUrl}?${Date.now()}`}
                      className="lazy"
                      style={{
                        width: "830px",
                        height: "500px",
                        borderRadius: "10px",
                      }}
                    />
                    <hr />
                    <span style={{ fontSize: "15pt" }}>
                      {uploadFileDb.fileContent}
                    </span>
                  </div>
                </div>

                <div className="spacer-single"></div>
              </div>
              {/*   */}

              {/*   */}
              <div id="sidebar" className="col-md-4 mt-5">
                {/*    */}
                <div className="widget widget-post">
                  <h4> </h4>

                  <div className="small-border" style={{}}></div>

                  <ul>
                    {/*     */}
                    {fileDb.slice(0, 5).map((news, index) => (
                      <li key={index}>
                        <span
                          className="date"
                          style={{ width: "30px", borderRadius: "5px" }}
                        >
                          {index + 1}
                        </span>

                        <Link to={`/news/${news.uuid}`}>
                          {" "}
                          <span>
                            {news.fileTitle.length > 15
                              ? `${news.fileTitle.substring(0, 15)}...`
                              : news.fileTitle}
                          </span>
                        </Link>
                      </li>
                    ))}

                    <Link to={`/news`}>
                      <h6></h6>
                    </Link>
                  </ul>
                </div>
                {/*   */}

                {/*    */}
                {/* TODO: ROLE_ADMIN    */}
                {userRole === "ROLE_ADMIN" && (
                  <div className="widget widget-post">
                    <h4> </h4>
                    <div className="small-border" style={{}}></div>
                    <div className="text-start">
                      {/*  */}
                      <Link to={`/news-detail/${uploadFileDb.uuid}`}>
                        <button
                          className="btn btn-primary me-3"
                          style={{ width: "120px ", height: "38px" }}
                        >
                          
                        </button>
                      </Link>
                      {/*   */}

                      {/*   */}
                      <button
                        className="btn btn-primary"
                        onClick={() => {
                          deleteImage(uploadFileDb.uuid);
                        }}
                        style={{ width: "120px ", height: "38px" }}
                      >
                        
                      </button>
                      {/*   */}
                    </div>
                  </div>
                )}
                {/*    */}

                {/*   */}
                <div className="widget">
                  <h4>SNS </h4>
                  <div className="small-border"></div>
                  <div className="widget">
                    <div className="de-color-icons">
                      <span onClick={shareOnTwitter}>
                        <i className="fa-brands fa-x-twitter fa-lg" style={{background: "black"}}></i>
                      </span>
                      <span onClick={shareOnFacebook}>
                        {/*      */}
                        <i className="fab fa-facebook fa-lg" style={{background: "black"}}></i>
                      </span>
                    </div>
                  </div>
                </div>
                {/*   */}
              </div>
              {/*    */}
            </div>
          </div>
        </section>
        {/*   */}

        {/* Toast Container */}
        <ToastContainer
          position="bottom-right"
          autoClose={5000}
          hideProgressBar
          newestOnTop={false}
          closeOnClick
          rtl={false}
          pauseOnFocusLoss
          draggable
          pauseOnHover
          style={{ width: "600px" }}
        />
      </div>
    </>
  );
}

export default NewsSingle;
