import React, { useEffect, useState } from 'react';
import designesis from '../../assets/js/designesia';
import customMarquee from '../../assets/js/custom-marquee';
import customSwiper2 from '../../assets/js/custom-swiper-2';
import IProduct from '../../types/IProduct';
import ProductService from '../../services/product/ProductService';
import { Link, useParams } from 'react-router-dom';
import NullPage from './NullPage';
import { Pagination } from '@mui/material';

function GameTagSimulation() {
    const [render, setRender] = useState<boolean>(false);

    useEffect(() => {
      customMarquee();
      customSwiper2();
      designesis();
    }, [render]);
  
    // todo    
    const [productList, setProductList] = useState<Array<IProduct>>([]);
    const initialCoast = {
      firstCoast: 0,
      lastCoast: 999999,
    };
    const [coast, setCoast] = useState<any>(initialCoast);
  
    //  TODO   ()  page( ), count(  ) , pageSize(3,6,9   1  )
    const [page, setPage] = useState<number>(1); //             1
    const [count, setCount] = useState<number>(1); //            1
    const [pageSize, setPageSize] = useState<number>(18); //1    20
    //   
    const [searchName, setSearchName] = useState<string>("");
    const [orderBy, setOrderBy] = useState<string>("DESC");
    const [message, setMessage] = useState<string>("  ");
  
    useEffect(() => {
      onClickOrderBy(orderBy);
    }, [page, pageSize]);
  
    //  todo   - 
    // Todo : Order By Desc
    const retrieveProductListDesc = () => {
      ProductService.getAllByThumbNailAndReviewCountFullJoinOrderByDesc(
        searchName,
        "",
        coast.firstCoast,
        coast.lastCoast,
        page - 1,
        pageSize
      )
        .then((response: any) => {
          const { product, totalPages } = response.data;
          setProductList(product);
          setCount(totalPages);
          console.log("product", product);
  
          setRender(true);
        })
        .catch((e: Error) => {
          console.log(e);
        });
    };
  
    // Todo : Order By Asc
    const retrieveProductListAsc = () => {
      ProductService.getAllByThumbNailAndReviewCountFullJoinOrderByAsc(
        searchName,
        "",
        coast.firstCoast,
        coast.lastCoast,
        page - 1,
        pageSize
      )
        .then((response: any) => {
          const { product, totalPages } = response.data;
          setProductList(product);
          setCount(totalPages);
          console.log("product", product);
  
          setRender(true);
        })
        .catch((e: Error) => {
          console.log(e);
        });
    };
  
    // TODO : Pagenation  
    // TODO :    -> page   
    const handlePageChange = (event: any, value: number) => {
      // value ==  
      setPage(value);
    };
  
    // Todo :   : 
    const coastSet = (object: any) => {
      setCoast(object);
    };
  
    // Todo :  
    const resetCoast = () => {
      setPage(1);
      setCoast(initialCoast);
      onClickOrderBy(orderBy);
    };
  
    // Todo :   
    const onChangeCoast = (e: any) => {
      const { name, value } = e.target;
      const onlyNumber = value.replace(/[^0-9]/g, ""); //   replace 
      setCoast({ ...coast, [name]: onlyNumber });
      retrieveProductListDesc();
    };
  
    // Todo :   
    const onChangeSearchName = (e: React.ChangeEvent<HTMLInputElement>) => {
      const searchName = e.target.value;
      setSearchName(searchName);
    };
  
    // Todo : OrderBy 
    const onClickOrderBy = (orderBy: string) => {
      setOrderBy(orderBy);
      console.log("orderBy", orderBy);
      if (orderBy === "DESC") {
        retrieveProductListDesc();
        setMessage("  ");
      } else if (orderBy === "ASC") {
        retrieveProductListAsc();
        setMessage("  ");
      }
    };
  
    // Todo :  
    const onClickSearchName = () => {
      setPage(1);
      onClickOrderBy(orderBy);
    };
  
    return (
      <>
        {render && (
          <div className="no-bottom no-top" id="content">
            <div id="top"></div>
  
            {/* < -- 2  : Most complete  -- >  */}
            <section className="jarallax">
              <img
                src="images/background/3.webp"
                className="jarallax-img"
                alt=""
              />
              <div className="de-gradient-edge-top"></div>
              <div className="de-gradient-edge-bottom"></div>
  
              <div className="container z-1000 ">
                <div className="row align-items-center">
                  <div className="col-lg-6">
                    <div className="subtitle wow fadeInUp mb-3">
                      Most complete
                    </div>
  
                    <h2 className="wow fadeInUp mb20" data-wow-delay=".2s">
                     
                    </h2>
                  </div>
                  <div className="col-lg-6 text-lg-end">
                    <a className="btn-main mb-sm-30" href="/games">
                      View all games
                    </a>
                  </div>
                </div>
  
                <div className="container">
                  <div className="row">
                    {/*   */}
                    <div className="col-md-8">
                      {/*    */}
                      <div
                        className="row g-4 sequence mt-5"
                        style={{ clear: "both" }}
                      >
                        {/* 1 */}
                        {productList &&
                        productList.map((value, index) => (
                          <>
                            <div
                              className="col-lg-4 col-md-4  wow fadeInRight"
                              data-wow-delay="0s"
                            >
                              <div
                                style={{
                                  width: "250px",
                                  height: "100%",
                                  fontSize: "12px",
                                }}
                              >
                                <div className="de-image-hover">
                                  <Link to={`/game-detail/${value.pid}`}>
                                    <img
                                      src={value.imgUrl}
                                      className="img-fluid"
                                      alt=""
                                      style={{
                                        marginBottom: "5px",
                                        borderRadius: "15px",
                                      }}
                                    />
                                    <span className="d-tagline">
                                      {value.tag}
                                    </span>

                                    <div className="d-text">
                                      <h3 style={{ fontSize: "17px" }}>
                                        {value.name}
                                      </h3>

                                      {value.discount > 0 ? (
                                        <>
                                          <p
                                            className="d-price"
                                            style={{
                                              display: "flex",
                                              alignItems: "center",
                                            }}
                                          >
                                            <span
                                              className="css-15fdr99"
                                              style={{
                                                display: "inline",
                                                fontSize: "8px",
                                              }}
                                            >
                                              {value.discount > 0 && (
                                                <div className="css-b0xoos">
                                                  {" "}
                                                  -{value.discount}%
                                                </div>
                                              )}
                                            </span>
                                            &nbsp; &nbsp;
                                            <del
                                              style={{
                                                display: "inline",
                                                fontSize: "15px",
                                              }}
                                            >
                                               {value.price.toLocaleString()}
                                            </del>
                                            <h4
                                              className="price"
                                              style={{
                                                display: "inline",
                                                marginTop: "12px",
                                              }}
                                            >
                                              &nbsp; {" "}
                                              {value.finalPrice.toLocaleString()}
                                            </h4>
                                          </p>
                                        </>
                                      ) : (
                                        <>
                                          <p
                                            className="d-price"
                                            style={{
                                              display: "flex",
                                              alignItems: "center",
                                            }}
                                          >
                                            <h4
                                              className="price"
                                              style={{ marginTop: "12px" }}
                                            >
                                              {(value.price > 0).toLocaleString()
                                                ? " " + value.price
                                                : ``}
                                            </h4>
                                          </p>
                                        </>
                                      )}
                                    </div>
                                  </Link>
                                </div>
                              </div>
                            </div>
                          </>
                        ))}
                      </div>
                      {render && (
                        <div className="container" id="paging">
                          {/*   */}
                          <Pagination
                            className="pagination"
                            count={count}
                            page={page}
                            siblingCount={1}
                            boundaryCount={1}
                            shape="rounded"
                            onChange={handlePageChange}
                            sx={{
                              color: "white", //  
                              display: "flex",
                              justifyContent: "center",
                              marginTop: "20px", //   
                              "& .Mui-selected": {
                                backgroundColor: "white", //   
                                color: "#1E1F22", //   
                                "&:hover": {
                                  backgroundColor: "white", //    
                                },
                              },
                              "& .MuiPaginationItem-root": {
                                fontSize: "1rem", //    
                                minWidth: "30px", //    
                                height: "30px", //   
                                color: "white", //     
                                "&:hover": {
                                  backgroundColor: "black", //      
                                },
                                "&.Mui-selected": {
                                  backgroundColor: "purple", //      
                                  color: "white", //      
                                  "&:hover": {
                                    backgroundColor: "purple", //       
                                  },
                                },
                              },
                            }}
                          />
                        </div>
                      )}
                    </div>
                    {/*   */}
  
                    {/*    */}
                    <div
                      id="sidebar"
                      className="col-md-3"
                      style={{ marginTop: "3rem" }}
                    >
                      {/*   search  */}
                      <div className="widget mb-1 wow fadeInUp">
                        <h4> </h4>
                        <div
                          className="small-border"
                          style={{ width: "100%" }}
                        ></div>
                        <div className="input-group">
                          <input
                            type="text"
                            className="form-control"
                            placeholder="   "
                            value={searchName}
                            onChange={onChangeSearchName}
                            style={{
                              height: "2.5rem",
                              backgroundColor: "rgba(255, 255, 255, .1)",
                              borderColor: "rgba(255, 255, 255, .1)",
                              color: "white",
                              width: "9rem",
                            }}
                          />
                          <div className="input-group-append">
                            <button
                              className="btn btn-outline-secondary overflow-hidden"
                              type="button"
                              onClick={onClickSearchName}
                              style={{
                                height: "2.5rem",
                                width: "5.5rem",
                                borderColor: "rgba(255, 255, 255, .1)",
                                color: "grey",
                              }}
                            >
                              Search
                            </button>
                          </div>
                        </div>
                      </div>
                      {/* search  */}
  
                      {/* (, , )  */}
                      <div className="widget wow fadeInUp">
                        <h4></h4>
                        <div
                          className="small-border"
                          style={{ width: "100%" }}
                        ></div>
                        <div>
                          <ul
                            // className="float-end float-sm-start "
                            data-wow-delay="0s"
                          >
                            <li>
                              <a
                                onClick={() =>
                                  coastSet({ firstCoast: 0, lastCoast: 5000 })
                                }
                              >
                                 ~ 5
                              </a>
                            </li>
                            <li>
                              <a
                                onClick={() =>
                                  coastSet({ firstCoast: 5000, lastCoast: 10000 })
                                }
                              >
                                5 ~ 1
                              </a>
                            </li>
                            <li>
                              <a
                                onClick={() =>
                                  coastSet({
                                    firstCoast: 10000,
                                    lastCoast: 20000,
                                  })
                                }
                              >
                                1 ~ 2
                              </a>
                            </li>
                            <li>
                              <a
                                onClick={() =>
                                  coastSet({
                                    firstCoast: 20000,
                                    lastCoast: 30000,
                                  })
                                }
                              >
                                2 ~ 3
                              </a>
                            </li>
                            <li>
                              <a
                                onClick={() =>
                                  coastSet({
                                    firstCoast: 30000,
                                    lastCoast: 50000,
                                  })
                                }
                              >
                                3 ~ 5
                              </a>
                            </li>
                            <li>
                              <a
                                onClick={() =>
                                  coastSet({
                                    firstCoast: 50000,
                                    lastCoast: 100000,
                                  })
                                }
                              >
                                5 ~ 10
                              </a>
                            </li>
                            <li className="mt-3">
                              <p>
                                
                                <div className="input-group">
                                  <span>
                                    <input
                                      type="text"
                                      className="form-control"
                                      placeholder="0"
                                      value={coast.firstCoast}
                                      name="firstCoast"
                                      onChange={onChangeCoast}
                                      style={{
                                        height: "2.5rem",
                                        backgroundColor:
                                          "rgba(255, 255, 255, .1)",
                                        borderColor: "rgba(255, 255, 255, .1)",
                                        color: "white",
                                        width: "5rem",
                                      }}
                                    />
                                  </span>
                                  <span>&nbsp;~&nbsp;</span>
                                  <span>
                                    <input
                                      type="text"
                                      className="form-control"
                                      placeholder="999,999,999"
                                      value={coast.lastCoast}
                                      name="lastCoast"
                                      onChange={onChangeCoast}
                                      style={{
                                        height: "2.5rem",
                                        backgroundColor:
                                          "rgba(255, 255, 255, .1)",
                                        borderColor: "rgba(255, 255, 255, .1)",
                                        color: "white",
                                        width: "5rem",
                                      }}
                                    />
                                  </span>
                                  <div className="input-group-append">
                                    <button
                                      className="btn btn-outline-secondary overflow-hidden"
                                      type="button"
                                      onClick={retrieveProductListDesc}
                                      style={{
                                        height: "2.5rem",
                                        width: "4rem",
                                        borderColor: "rgba(255, 255, 255, .1)",
                                        color: "grey",
                                      }}
                                    >
                                      Search
                                    </button>
                                  </div>
                                </div>
                              </p>
                            </li>
                            <li>
                              <div className="widget widget_tags">
                                <li>
                                  <a onClick={resetCoast}>
                                    {coast.firstCoast}  ~ {coast.lastCoast}{" "}
                                    &nbsp;&nbsp;x
                                  </a>
                                </li>
                              </div>
                            </li>
                          </ul>
                        </div>
                      </div>
                      {/*  (, , )  */}
  
                      {/*   */}
                      <div className="widget wow fadeInUp">
                        <h4></h4>
                        <div
                          className="small-border"
                          style={{ width: "100%" }}
                        ></div>
                        <div>
                          <ul data-wow-delay="0s">
                            <li>
                              <a onClick={() => onClickOrderBy("DESC")}>
                                  
                              </a>
                            </li>
                            <li>
                              <a onClick={() => onClickOrderBy("ASC")}>
                                  
                              </a>
                            </li>
                            <li>
                              <div className="widget widget_tags">
                                <li>
                                  <a onClick={() => onClickOrderBy("DESC")}>
                                    {message} &nbsp;&nbsp;x
                                  </a>
                                </li>
                              </div>
                            </li>
                          </ul>
                        </div>
                      </div>
                      {/*   */}
  
                      {/*    */}
                      <div className="widget widget-text wow fadeInUp">
                        <h4> </h4>
                        <div className="small-border"></div>
                        <p className="small no-bottom"></p>
                      </div>
                      {/*    */}
                    </div>
                    {/*    */}
                  </div>
                </div>
              </div>
            </section>
            {/* < -- 2  : Most complete  -- >  */}
  
            {/* < -- 3  : Start your game  -- > */}
            <section className="no-bottom">
              <div className="container">
                <div className="row">
                  <div className="col-lg-12">
                    <div className="padding60 sm-padding40 sm-p-2 jarallax position-relative">
                      <img
                        src="images/background/1.webp"
                        className="jarallax-img"
                        alt=""
                      />
                      <div className="row">
                        <div className="col-lg-6">
                          <div className="subtitle wow fadeInUp mb-3">
                            Start your game
                          </div>
                          <h2 className="wow fadeInUp" data-wow-delay=".2s">
                                 
                          </h2>
                          <p className="wow fadeInUp">
                                  !
                          </p>
                          <div className="spacer-10"></div>
                          <a className="btn-main mb10 wow fadeInUp" href="/games">
                              
                          </a>
                        </div>
                      </div>
  
                      <img
                        src="images/misc/avatar.webp"
                        className="sm-hide position-absolute bottom-0 end-0 wow fadeIn"
                        alt=""
                      />
                    </div>
                  </div>
                </div>
              </div>
            </section>
            {/* < -- 3  : Start your game  -- > */}
  
            {/* 4  : we accept  */}
            <section>
              <div className="container">
                <div className="row align-items-center">
                  <div className="col-lg-6">
                    <div className="subtitle wow fadeInUp mb-3">
                      Payment Methods
                    </div>
                    <h2 className="wow fadeInUp" data-wow-delay=".2s">
                      We accept
                    </h2>
                  </div>
                  <div className="col-lg-6">
                    <div className="row g-4">
                      <div className="col-sm-2 col-4">
                        <div
                          className="p-2 rounded-10"
                          data-bgcolor="rgba(255, 255, 255, .05)"
                        >
                          <img
                            src={require("../../assets/images/payments/visa.webp")}
                            className="img-fluid"
                            alt=""
                          />
                        </div>
                      </div>
                      <div className="col-sm-2 col-4">
                        <div
                          className="p-2 rounded-10"
                          data-bgcolor="rgba(255, 255, 255, .05)"
                        >
                          <img
                            src={require("../../assets/images/payments/mastercard.webp")}
                            className="img-fluid"
                            alt=""
                          />
                        </div>
                      </div>
                      <div className="col-sm-2 col-4">
                        <div
                          className="p-2 rounded-10"
                          data-bgcolor="rgba(255, 255, 255, .05)"
                        >
                          <img
                            src={require("../../assets/images/payments/paypal.webp")}
                            className="img-fluid"
                            alt=""
                          />
                        </div>
                      </div>
                      <div className="col-sm-2 col-4">
                        <div
                          className="p-2 rounded-10"
                          data-bgcolor="rgba(255, 255, 255, .05)"
                        >
                          <img
                            src={require("../../assets/images/payments/skrill.webp")}
                            className="img-fluid"
                            alt=""
                          />
                        </div>
                      </div>
                      <div className="col-sm-2 col-4">
                        <div
                          className="p-2 rounded-10"
                          data-bgcolor="rgba(255, 255, 255, .05)"
                        >
                          <img
                            src={require("../../assets/images/payments/jcb.webp")}
                            className="img-fluid"
                            alt=""
                          />
                        </div>
                      </div>
                      <div className="col-sm-2 col-4">
                        <div
                          className="p-2 rounded-10"
                          data-bgcolor="rgba(255, 255, 255, .05)"
                        >
                          <img
                            src={require("../../assets/images/payments/american-express.webp")}
                            className="img-fluid"
                            alt=""
                          />
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </section>
            {/* 4  : we accept  */}
          </div>
        )}
        {/* <!-- content begin --> */}
  
        {/* <!-- content close --> */}
      </>
    );
  }



export default GameTagSimulation;
