import React, { useEffect, useState } from "react";
import Pagination from "@mui/material/Pagination";
import IProduct from "../../../types/IProduct";
import AdminProductService from "../../../services/admin/AdminProductService";
import { useNavigate, useParams } from "react-router-dom";
import designesis from "../../../assets/js/designesia";
import ThumbNailService from "../../../services/product/ThumbNailService";
import IThumbNail from "../../../types/IThumbNail";
import { ToastContainer, toast } from "react-toastify";

function AdminControlPanelModify() {
  useEffect(() => {
    designesis();
  }, []);
  // TODO:  
  // TODO: adminProductList  
  let [adminProductList, setAdminProductList] = useState<Array<IProduct>>([]);
  let [tag, setTag] = useState<Array<string>>([]);

  // TODO:   adminProduct 
  // initialAdminProduct  
  const initialAdminProduct = {
    pid: "",
    name: "",
    shortDescription: "",
    imgUrl: "",
    price: 0,
    finalPrice: 0,
    tag: "",
    discount: 0,
    uuid: "",
  };
  const [adminProduct, setAdminProduct] =
    useState<IProduct>(initialAdminProduct);

  //  
  const [searchName, setSearchName] = useState<string>("");
  const [selectBox, setSelectBox] = useState<string>("");

  //   
  let navigate = useNavigate();
  //      
  const [message, setMessage] = useState<string>("");

  // TODO :  () : page( ), count(  ) , pageSize(3,6,9  : 1  )
  const [page, setPage] = useState<number>(1); //           :  1
  const [count, setCount] = useState<number>(1); //           :  1
  const [pageSize, setPageSize] = useState<number>(20); // 1  :  20

  const initialFileDb = {
    uuid: null,
    fileUrl: "",
  };

  const [uploadFileDb, setUploadFileDb] = useState<IThumbNail>(initialFileDb);
  const [selectedFiles, setSelectedFiles] = useState<FileList>();
  const [previewImage, setPreviewImage] = useState<string | undefined>(
    uploadFileDb.fileUrl
  );

  // TODO:  
  // TODO : Pagenation  
  // TODO :    -> page   
  const handlePageChange = (event: any, value: number) => {
    // value ==  
    setPage(value);
  };

  // todo :  
  useEffect(() => {
    reteiveAdminProductList();
  }, [page, pageSize]);

  // todo :   
  const reteiveAdminProductList = () => {
    AdminProductService.getAllByTag(searchName, selectBox, page - 1, pageSize)
      .then((response: any) => {
        const { product, tag, totalPages } = response.data;
        setAdminProductList(product);
        setTag(tag);
        // Todo :  
        setCount(totalPages);
        // 
        console.log(response.data);
      })
      .catch((e: Error) => {
        console.log(e);
      });
  };

  // todo:  
  const getAdminProduct = (pid: string) => {
    AdminProductService.get(pid) //   
      .then((response: any) => {
        setAdminProduct(response.data);
        getFileDb(response.data.uuid);
        console.log(response.data);
      })
      .catch((e: Error) => {
        console.log(e);
      });
  };

  // Todo :   
  const handleInputChange = (event: any) => {
    const { name, value } = event.target;
    if (value >= 0) {
      setAdminProduct({ ...adminProduct, [name]: value });
    }
    console.log(adminProduct.discount);
  };

  // todo:     
  const updateFileDb = () => {
    let currentFile = selectedFiles?.[0];
    console.log(" uuid ", uploadFileDb.uuid);

    if (currentFile == null) {
      return updateProductDiscount(uploadFileDb.uuid);
    }

    ThumbNailService.updateFileDb(uploadFileDb, currentFile)
      .then((response: any) => {
        console.log("updateFileDb   ", response.data);
        updateProductDiscount(response.data);
      })
      .catch((e: Error) => {
        console.log(e);
        toastMessage(
          "   .  "
        );
      });
  };

  // todo:     
  const updateProductDiscount = (uuid: any) => {
    let data = {
      pid: adminProduct.pid,
      name: adminProduct.name,
      shortDescription: adminProduct.shortDescription,
      imgUrl: adminProduct.imgUrl,
      price: adminProduct.price,
      finalPrice:
        Math.ceil(
          adminProduct.price * ((100 - adminProduct.discount) / 10000)
        ) * 100,
      tag: adminProduct.tag,
      discount: adminProduct.discount,
      uuid: uuid,
    };

    AdminProductService.update(data.pid, data) //   
      .then((response: any) => {
        console.log(response.data);
        alert(" .");
        window.location.reload();
      })
      .catch((e: Error) => {
        if ("Request failed with status code 400" == e.message) {
          toastMessage("  .");
        } else {
          toastMessage("  .  ");
        }
        console.log(e);
      });
  };

  // todo:     
  const deleteProduct = () => {
    // Todo :  
    AdminProductService.remove(adminProduct.pid) //  
      .then((response: any) => {
        console.log(response.data);
      })
      .catch((e: Error) => {
        console.log(e);
      });

    // Todo :  
    ThumbNailService.deleteFile(adminProduct.uuid)
      .then((response: any) => {
        console.log(response.data);
        toastMessage(" .");
        reteiveAdminProductList();
      })
      .catch((e: Error) => {
        console.log(e);
      });
  };

  const toastMessage = (message: string) => {
    toast.info(
      <div>
        <div>{message}</div>
        <div style={{ whiteSpace: "pre-line" }}>{adminProduct.name}</div>
      </div>,
      {
        position: "bottom-right",
        autoClose: 5000,
        hideProgressBar: true,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
      }
    );
  };

  //    
  const onChangeSearchName = (e: React.ChangeEvent<HTMLInputElement>) => {
    const searchName = e.target.value;
    setSearchName(searchName);
  };

  //  Select    
  const onChangeSelectBox = (e: any) => {
    const selectBox = e.target.value;

    setSelectBox(selectBox);
  };

  //Todo :  
  const getFileDb = (uuid: string) => {
    ThumbNailService.getFileDb(uuid)
      .then((response: any) => {
        setUploadFileDb(response.data);
        setPreviewImage(response.data.fileUrl); //      
        console.log(" ", response.data);
      })
      .catch((e: Error) => {
        console.log(e);
      });
  };

  const selectFile = (event: any) => {
    setSelectedFiles(event.target.files as FileList);

    const file = event.target.files[0];
    const reader = new FileReader();

    reader.onloadend = () => {
      setPreviewImage(reader.result as string);
    };

    if (file) {
      reader.readAsDataURL(file);
    }
  };

  return (
    <>
      <div className="no-bottom no-top" id="content">
        <div id="top"></div>
        {/* <!--  section  --> */}
        <section id="subheader" className="jarallax">
          <img
            src={require("../../../assets/images/background/bg-grid.webp")}
            className="jarallax-img"
            alt=""
          />
          <div className="container z-1000">
            <div className="row">
              {/*   */}
              <div className="col-lg-12">
                <div className="subtitle wow fadeInUp mb-3">Admin</div>
              </div>
              {/*   */}

              {/*   */}
              <div className="col-lg-6">
                <h2 className="wow fadeInUp mb20" data-wow-delay=".2s">
                   / 
                </h2>
              </div>
              {/*   */}

              {/*    */}
              <div className="row mt-5 justify-content-center">
                {/* w-50 :  , mx-auto : (margin: 0 auto), justify-content-center */}
                <div className="col-12 w-50 input-group mb-3" id="form_sb">
                  <input
                    type="text"
                    className="form-control"
                    placeholder="   "
                    value={searchName}
                    onChange={onChangeSearchName}
                  />
                  <div className="input-group-append">
                    <button
                      className="btn btn-outline-primary overflow-hidden"
                      type="button"
                      onClick={reteiveAdminProductList}
                      style={{ height: "49px", width: "80px" }}
                    >
                      Search
                    </button>
                  </div>
                </div>
              </div>
              {/*    */}

              {/*    */}
              <select
                className="form-select col-md-4 offset-10"
                aria-label="Default select example"
                style={{ width: "120px" }}
                onChange={onChangeSelectBox}
              >
                <option selected value="">
                  
                </option>
                {tag &&
                  tag.map((value: any, index) => (
                    <option value={value} key={index}>
                      {value}
                    </option>
                  ))}
              </select>
              {/*    */}
            </div>
          </div>
        </section>
        {/* <!--  section  --> */}

        {/*   */}
        <section className="no-top">
          <div className="container">
            <div className="row">
              <div className="col-lg-12 text-center wow fadeInUp">
                <div className="switch-set">
                  <div className="spacer-20"></div>
                </div>
              </div>
              {/*  (+)  */}
              <div className="col-md-12">
                <table className="table table-pricing dark-style text-center">
                  <thead>
                    <tr>
                      <th style={{ paddingLeft: "50px", paddingRight: "50px" }}>
                        ID
                      </th>
                      <th style={{ paddingLeft: "15px", paddingRight: "15px" }}>
                        
                      </th>
                      <th style={{ paddingLeft: "15px" }}></th>
                      <th style={{ paddingLeft: "40px", paddingRight: "40px" }}>
                        
                      </th>
                      <th style={{ paddingLeft: "40px", paddingRight: "40px" }}>
                        
                      </th>
                      <th style={{ paddingLeft: "40px", paddingRight: "40px" }}>
                        
                      </th>
                      <th style={{ paddingLeft: "10px", paddingRight: "20px" }}>
                        /
                      </th>
                    </tr>
                  </thead>
                  {/*  ()   */}
                  <tbody id="my-accordion">
                    {adminProductList &&
                      adminProductList.map((data, index) => (
                        <>
                          <tr key={index} style={{ height: "170px" }}>
                            {/*  */}
                            <th
                              style={{
                                paddingLeft: "30px",
                                paddingRight: "30px",
                              }}
                            >
                              {data.pid}
                            </th>
                            {/*   */}
                            <td>
                              <img
                                src={data.imgUrl}
                                style={{ width: "250px", borderRadius: "15px" }}
                              ></img>
                            </td>
                            {/*   */}
                            <td
                              className="d-spc"
                              style={{ width: "250px", paddingLeft: "15px" }}
                            >
                              {data.name}
                            </td>
                            {/*   */}
                            {data.price ? (
                              <td>{data.price} </td>
                            ) : (
                              <td> </td>
                            )}
                            {/*   */}
                            <td> {data.tag} </td>
                            {/*  */}
                            <td>{data.discount} %</td>
                            {/* /  ( ) */}
                            <td
                              style={{
                                paddingLeft: "30px",
                                paddingRight: "30px",
                              }}
                            >
                              <a
                                data-bs-toggle="collapse"
                                data-bs-target={"#collapse" + index}
                                role="button"
                                aria-expanded="false"
                                aria-controls={"collapse" + index}
                                className="btn-main opt-1 me-2"
                                onClick={() => getAdminProduct(data.pid)}
                                style={{
                                  width: "170px",
                                  fontSize: "15px",
                                }}
                              >
                                 
                              </a>
                            </td>
                          </tr>
                          {/* adminProductList details  */}
                          <tr>
                            <td
                              colSpan={7}
                              style={{ padding: "10px 0 10px 0" }}
                            >
                              {/*   */}
                              <div
                                className="accordion-collapse collapse"
                                id={"collapse" + index}
                                aria-labelledby={"heading" + index}
                                data-bs-parent="#my-accordion"
                              >
                                <div className="my-card card-body">
                                  <table className="table table-pricing dark-style text-center">
                                    <thead>
                                      <tr style={{ height: "60px" }}>
                                        <th
                                          style={{
                                            padding: "0 0 0 0",
                                            verticalAlign: "middle",
                                          }}
                                        >
                                           
                                        </th>
                                        <th
                                          style={{
                                            padding: "0 0 0 0",
                                            verticalAlign: "middle",
                                          }}
                                        >
                                           
                                        </th>
                                        <th
                                          style={{
                                            padding: "0 0 0 0",
                                            verticalAlign: "middle",
                                          }}
                                        >
                                           
                                        </th>
                                        <th
                                          style={{
                                            padding: "0 0 0 0",
                                            verticalAlign: "middle",
                                          }}
                                        >
                                           
                                        </th>
                                      </tr>
                                    </thead>
                                    <tbody>
                                      <tr>
                                        {/*   */}
                                        <td
                                          style={{
                                            padding: "10px 0 10px 0",
                                            verticalAlign: "middle",
                                          }}
                                        >
                                          {data.price}  -{" "}
                                          {Math.ceil(
                                            data.price *
                                              (adminProduct.discount / 100)
                                          )}{" "}
                                           = {/*    */}
                                          {data.price * ((100 - adminProduct.discount) / 100)}
                                          
                                        </td>
                                        {/*  */}
                                        <td
                                          style={{
                                            padding: "10px 0 10px 0",
                                            verticalAlign: "middle",
                                          }}
                                        >
                                          {adminProduct.price > 0 && (
                                            <>
                                              {" "}
                                              <input
                                                style={{
                                                  width: "150px",
                                                  height: "35px",
                                                  display: "inline",
                                                  paddingTop: "5px",
                                                  marginRight: "5px",
                                                }}
                                                type="number"
                                                id="discount"
                                                required
                                                className="form-control ms-3"
                                                value={adminProduct.discount}
                                                onChange={handleInputChange}
                                                placeholder="unitPrice"
                                                name="discount"
                                              />
                                            </>
                                          )}

                                          {adminProduct.price === 0 &&
                                            adminProduct.discount === 0 && (
                                              <>
                                                {" "}
                                                <input
                                                  style={{
                                                    width: "150px",
                                                    height: "35px",
                                                    display: "inline",
                                                    paddingTop: "5px",
                                                    marginRight: "5px",
                                                  }}
                                                  type="number"
                                                  id="discount"
                                                  required
                                                  disabled
                                                  className="form-control ms-3"
                                                  value={adminProduct.discount}
                                                  onChange={handleInputChange}
                                                  placeholder="unitPrice"
                                                  name="discount"
                                                />
                                              </>
                                            )}
                                        </td>
                                        <td
                                          style={{
                                            padding: "10px 0 10px 0",
                                            verticalAlign: "bottom",
                                          }}
                                        >
                                          {/* upload / start */}
                                          <input
                                            type="file"
                                            onChange={selectFile}
                                            style={{
                                              color: "black",
                                              backgroundColor: "white",
                                              width: "200px",
                                              height: "30px",
                                              fontSize: "14px",
                                            }}
                                          />
                                          <br />
                                          <img
                                            src={previewImage}
                                            style={{
                                              width: "200px",
                                              borderRadius: "15px",
                                              paddingTop: "10px",
                                            }}
                                          />
                                        </td>
                                        {/*  */}
                                        <td
                                          style={{
                                            padding: "10px 0 10px 0",
                                            verticalAlign: "middle",
                                          }}
                                        >
                                          <button
                                            className="btn-main opt-1"
                                            onClick={deleteProduct}
                                            style={{
                                              width: "170px",
                                              fontSize: "15px",
                                            }}
                                          >
                                              
                                          </button>
                                          <br />
                                          <button
                                            className="btn-main opt-1 mt-3"
                                            onClick={updateFileDb}
                                            style={{
                                              width: "170px",
                                              fontSize: "15px",
                                            }}
                                          >
                                              
                                          </button>
                                        </td>
                                      </tr>
                                    </tbody>
                                  </table>
                                </div>
                              </div>
                            </td>
                          </tr>
                        </>
                      ))}
                  </tbody>
                  {/*  ()   */}
                </table>
                {/*   */}
                <Pagination
                  className="pagination"
                  count={count}
                  page={page}
                  siblingCount={1}
                  boundaryCount={1}
                  shape="rounded"
                  onChange={handlePageChange}
                  sx={{
                    backgroundColor: "#1E1F22", //  
                    color: "white", //  
                    display: "flex",
                    justifyContent: "center",
                    marginTop: "20px", //   
                    "& .Mui-selected": {
                      backgroundColor: "white", //   
                      color: "#1E1F22", //   
                      "&:hover": {
                        backgroundColor: "white", //    
                      },
                    },
                    "& .MuiPaginationItem-root": {
                      fontSize: "1rem", //    
                      minWidth: "30px", //    
                      height: "30px", //   
                      color: "white", //     
                      "&:hover": {
                        backgroundColor: "black", //      
                      },
                      "&.Mui-selected": {
                        backgroundColor: "purple", //      
                        color: "white", //      
                        "&:hover": {
                          backgroundColor: "purple", //       
                        },
                      },
                    },
                  }}
                />
              </div>
              {/*  ()  */}
            </div>
          </div>
        </section>
        {/*   */}

        {/* < -- 2  : Premium Game Server  -- > */}
        <section className="no-bottom">
          <div className="container pb-5">
            <div className="row">
              <div className="col-lg-6">
                <div className="subtitle wow fadeInUp mb-3">
                  Incredibly features
                </div>
                <h2 className="wow fadeInUp mb20" data-wow-delay=".2s">
                    
                </h2>
              </div>

              <div className="col-lg-6"></div>

              <div
                className="col-lg-3 col-md-6 mb-sm-20 wow fadeInRight"
                data-wow-delay="0s"
              >
                <div>
                  <img src="images/icons/1.png" className="mb20" alt="" />
                  <h4>Super Quick Setup</h4>
                  <p>       .</p>
                </div>
              </div>

              <div
                className="col-lg-3 col-md-6 mb-sm-20 wow fadeInRight"
                data-wow-delay=".2s"
              >
                <div>
                  <img src="images/icons/2.png" className="mb20" alt="" />
                  <h4>Premium Hardware</h4>
                  <p>    .</p>
                </div>
              </div>

              <div
                className="col-lg-3 col-md-6 mb-sm-20 wow fadeInRight"
                data-wow-delay=".4s"
              >
                <div>
                  <img src="images/icons/3.png" className="mb20" alt="" />
                  <h4>DDos Protection</h4>
                  <p>     .</p>
                </div>
              </div>

              <div
                className="col-lg-3 col-md-6 mb-sm-20 wow fadeInRight"
                data-wow-delay=".6s"
              >
                <div>
                  <img src="images/icons/4.png" className="mb20" alt="" />
                  <h4>Fast Support</h4>
                  <p>
                    24       !
                  </p>
                </div>
              </div>
            </div>
          </div>
          {/* Toast Container */}
          <ToastContainer
            position="bottom-right"
            autoClose={5000}
            hideProgressBar
            newestOnTop={false}
            closeOnClick
            rtl={false}
            pauseOnFocusLoss
            draggable
            pauseOnHover
            style={{ width: "600px" }}
          />
        </section>
        {/* < -- 2  : Premium Game Server  -- > */}
      </div>
    </>
  );
}

export default AdminControlPanelModify;
