import React, { useEffect, useState } from "react";
import designesis from "../../../assets/js/designesia";
import QnaService from "../../../services/qna/QnaService";
import IQna from "../../../types/IQna";
import { Link, useParams } from "react-router-dom";
import { Pagination } from "@mui/material";
import UserQnaService from "../../../services/user/UserQnaService";
import { useSelector } from "react-redux";
import { RootState } from "../../../store/store";

function UserQnaList() {
  
  //   (userId)
  const { user } = useSelector((state: RootState) => state.auth);

  useEffect(() => {
    designesis();
  }, []);

  // TODO:  
  //   (keyword)
  const [searchKeyword, setSearchKeyword] = useState<string>("");
  // qna  
  let [qnaList, setQnaList] = useState<Array<IQna>>([]);


  // TODO :  () : page( ), count(  ) , pageSize(3,6,9  : 1  )
  const [page, setPage] = useState<number>(1); //           :  1
  const [count, setCount] = useState<number>(1); //           :  1
  const [pageSize, setPageSize] = useState<number>(6); // 1  :  9

  // TODO : Pagenation   
  // TODO :    -> page   
  const handlePageChange = (event: any, value: number) => {
    // value ==  
    setPage(value);
  };

  //   input  
  const onChangeSearchKeyword = (e: any) => {
    setSearchKeyword(e.target.value); //  -> 
  };

  // TODO:  
  // todo :  ( )
  useEffect(() => {
    retrieveQnaListByUserId();
  }, [page, pageSize]);

  // todo: QnaList  
  const retrieveQnaListByUserId = () => {
    UserQnaService.getAllByUserId(user?.userId,searchKeyword , page - 1, pageSize)
      .then((response: any) => {
        const { qna, totalPages } = response.data;
        setQnaList(qna);
        // Todo :  
        setCount(totalPages);
        // 
        console.log(response.data);
      })
      .catch((e: Error) => {
        console.log(e);
      });
  };

  return (
    <>
      <div className="no-bottom no-top" id="content">
        <div id="top"></div>
        {/*    */}
        {/* <!-- section begin --> */}
        <section id="subheader" className="jarallax">
          <div className="de-gradient-edge-bottom"></div>
          <img
            src={require("../../../assets/images/background/space.webp")}
            className="jarallax-img"
            alt=""
          />
          <div className="container z-1000">
            <div className="row">
              <div className="col-lg-12 mb-3 text-center">
                <div className="subtitle wow fadeInUp mb-3">User</div>
                <h2 className="wow fadeInUp mb-3" data-wow-delay=".2s">
                   QnA
                </h2>
              </div>

              <div className="col-md-8 offset-md-2 text-center wow fadeInUp mb-3">
                <form
                  action="#"
                  className="row"
                  data-wow-delay="1.25s"
                  id="form_sb"
                  method="post"
                  name="form_sb"
                >
                  <div className="col">
                    <div className="spacer-10"></div>
                    <input
                      className="form-control"
                      id="domain_name"
                      name="domain_name"
                      placeholder="  "
                      type="text"
                      value={searchKeyword}
                      onChange={onChangeSearchKeyword}
                    />{" "}
                    <a
                      id="btn-submit"
                      href="javascript:;"
                      onClick={retrieveQnaListByUserId}
                    >
                      <i className="arrow_right"></i>
                    </a>
                    <div className="clearfix"></div>
                  </div>
                </form>
                <div className="spacer-20"></div>
              </div>
            </div>
          </div>
        </section>
        {/* <!-- section close --> */}
        {/*    */}

        {/* QnA   */}
        <section className="no-top wow fadeInUp">
          {/* QnA    */}
          <div className="container">
            <div className="row g-4">
              {/*      */}
              <h2 className="wow fadeInUp mb20" data-wow-delay=".2s">
                  
              </h2>
              {qnaList &&
                qnaList
                  .filter((data) => data.answerYn == "N")
                  .map((data) => (
                    <>
                      <div className="col-lg-13 col-md-13">
                        <div className="de-box-cat">
                          <i className="fa fa-folder-open-o"></i>
                          <h3>
                            {data.questioner} &nbsp;
                            <span>({data.answerYn})</span>
                          </h3>
                          <div className="d-subtitle">
                            <h4></h4>
                          </div>
                          <p>{data.question}</p>
                          <Link
                            role="button"
                            className="btn-main opt-1 me-2"
                            style={{
                              width: "180px",
                              fontSize: "15px",
                            }}
                            to={`/user-qna/${data.qid}`}
                          >
                            Question Detail
                          </Link>
                        </div>
                      </div>
                    </>
                  ))}

              {/* ui   */}
              <div className="spacer-single"></div>

              <div className="container" id="paging">
                <Pagination
                  className="pagination"
                  count={count}
                  page={page}
                  siblingCount={1}
                  boundaryCount={1}
                  shape="rounded"
                  onChange={handlePageChange}
                  sx={{
                    backgroundColor: "#1E1F22", //  
                    color: "white", //  
                    display: "flex",
                    justifyContent: "center",
                    marginTop: "20px", //   
                    "& .Mui-selected": {
                      backgroundColor: "white", //   
                      color: "#1E1F22", //   
                      "&:hover": {
                        backgroundColor: "white", //    
                      },
                    },
                    "& .MuiPaginationItem-root": {
                      fontSize: "1rem", //    
                      minWidth: "30px", //    
                      height: "30px", //   
                      color: "white", //     
                      "&:hover": {
                        backgroundColor: "black", //      
                      },
                      "&.Mui-selected": {
                        backgroundColor: "purple", //      
                        color: "white", //      
                        "&:hover": {
                          backgroundColor: "purple", //       
                        },
                      },
                    },
                  }}
                />
              </div>
              {/*      */}

              {/*  div */}
              <div className="spacer-single"></div>

              {/*      */}
              <h2 className="wow fadeInUp mb20" data-wow-delay=".2s">
                  
              </h2>
              {qnaList &&
                qnaList
                  .filter((data) => data.answerYn == "Y")
                  .map((data) => (
                    <>
                      <div className="col-lg-13 col-md-13">
                        <div className="de-box-cat">
                          <i className="fa fa-folder-open-o"></i>
                          <h3>
                            {data.questioner} &nbsp;
                            <span>({data.answerYn})</span>
                          </h3>
                          <div className="d-subtitle">
                            <h4></h4>
                          </div>
                          <p>{data.question}</p>
                          <div className="d-subtitle">
                            <h4>{data.answerer}</h4>
                          </div>
                          <p>{data.answer}</p>
                          <Link
                            role="button"
                            className="btn-main opt-1 me-2"
                            style={{
                              width: "180px",
                              fontSize: "15px",
                            }}
                            to={`/user-qna/${data.qid}`}
                          >
                            Question Detail
                          </Link>
                        </div>
                      </div>
                    </>
                  ))
                  .slice((page - 1) * pageSize, page * pageSize)}

              {/* ui   */}
              <div className="spacer-single"></div>

              <Pagination
                className="pagination"
                count={count}
                page={page}
                siblingCount={1}
                boundaryCount={1}
                shape="rounded"
                onChange={handlePageChange}
                sx={{
                  color: "white", //  
                  display: "flex",
                  justifyContent: "center",
                  marginTop: "20px", //   
                  "& .Mui-selected": {
                    backgroundColor: "white", //   
                    color: "#1E1F22", //   
                    "&:hover": {
                      backgroundColor: "white", //    
                    },
                  },
                  "& .MuiPaginationItem-root": {
                    fontSize: "1rem", //    
                    minWidth: "30px", //    
                    height: "30px", //   
                    color: "white", //     
                    "&:hover": {
                      backgroundColor: "black", //      
                    },
                    "&.Mui-selected": {
                      backgroundColor: "purple", //      
                      color: "white", //      
                      "&:hover": {
                        backgroundColor: "purple", //       
                      },
                    },
                  },
                }}
              />
              {/*      */}

              {/*  div */}
              <div className="spacer-double"></div>

              {/*     */}
              <div className="col-lg-12">
                <div
                  className="padding40 rounded-10"
                  data-bgcolor="rgba(255, 255, 255, .1)"
                >
                  <div className="row align-items-center">
                    <div className="col-lg-1">
                      <img
                        src={require("../../../assets/images/icons/4.png")}
                        alt=""
                        className="img-responsive"
                      />
                    </div>
                    <div className="col-lg-9">
                      <h3>    ?</h3>
                    </div>
                    <div className="col-lg-2 text-lg-end">
                      <Link
                        to={`/add-user-qna/${user?.userId}`}
                        className="btn-main"
                        style={{ width: "120px", fontSize: "15px" }}
                      >
                        
                      </Link>
                    </div>
                  </div>
                </div>
              </div>
              {/*     */}
            </div>
          </div>
        </section>
        {/* QnA ()  */}
      </div>
    </>
  );
}

export default UserQnaList;
