import React, { useEffect } from "react";
import {
  useLocation,
  useNavigate,
  useParams,
  useSearchParams,
} from "react-router-dom";
import IOauth2 from "../../types/auth/IOauth2";
import { useAppDispatch } from "../../store/store";
import { oauthLogin } from "../../store/slices/auth";
import designesis from "../../assets/js/designesia";

function Oauth2() {
  useEffect(() => {
    designesis();
    console.log("registrationId",registrationId);
    console.log("code" , code);
  }, []);
  let navigate = useNavigate();
  let { registrationId } = useParams();

  const [searchParams, setSearchParams] = useSearchParams(); //   searchParams  
  const code = searchParams.get("code"); // offset   

  let app: IOauth2 = {
    code,
    registrationId,
  };

  // Todo :   
  // Todo :     : useAppDispatch()
  // Todo :   : dispatch()
  // Todo :   : dispatch(login) , dispatch(logout)
  const dispatch = useAppDispatch();

  // Todo :    
  dispatch(oauthLogin(app))
    // Todo : .unwrap() : redux     
    .unwrap()
    .then(() => {
      navigate("/");
      window.location.reload(); //  
    })
    .catch((e: Error) => {
      console.log(e);
      alert("    .");
      navigate("/login");
    });

  return (
    <>
      <div className="float-text show-on-scroll">
        <span>
          <a href="#">Scroll to top</a>
        </span>
      </div>
      <div className="scrollbar-v show-on-scroll"></div>
      {/* <!-- page preloader begin --> */}
      <div id="de-loader"></div>
      {/* <!-- page preloader close --> */}
    </>
  );
}

export default Oauth2;
