// Todo :    :  , 
import { createSlice, createAsyncThunk } from "@reduxjs/toolkit";
import IUser from "../../types/auth/IUser";
import AuthService from "../../services/auth/AuthService";
import IAuth from "../../types/auth/IAuth";
import IOauth2 from "../../types/auth/IOauth2";

// Todo : localStorage  user   :  ->   : JSON.parse
// user = webToken 
const user = JSON.parse(localStorage.getItem("user") || "null");

// Todo  : 
// Async    : 
// createAsyncThunk( , ( , thunkAPI) => {}) :    

// thunkAPI      :   :   
//                ->  : thunkAPI.rejectWithValue()

// Todo : async ~ await   ()
//   :
// promise -> .then().catch() [ ]
// async () => { await  };      [] :      

/* ------    --------*/
// Todo :   ( )
export const register = createAsyncThunk(
  "auth/register", //    
  async (user: IUser, thunkAPI) => {
    //   ==  
    try {
      const response = await AuthService.register(user); //  service 
      return response.data;
    } catch (error: any) {
      const message =
        (error.response &&
          error.response.data &&
          error.response.data.message) ||
        error.message ||
        error.toString();
      return thunkAPI.rejectWithValue("  : " + message);
    }
  }
);

// Todo :   ( )
export const login = createAsyncThunk(
  // 
  "auth/login",
  //  
  async (user: IUser, thunkAPI) => {
    try {
      const data = await AuthService.login(user); //  service 
      return { user: data };
    } catch (error: any) {
      const message =
        (error.response &&
          error.response.data &&
          error.response.data.message) ||
        error.message ||
        error.toString();
      return thunkAPI.rejectWithValue("  : " + message);
    }
  }
);

// Todo :   ( )
export const oauthLogin = createAsyncThunk(
  // 
  "auth/oauthLogin",
  //  
  async (app: IOauth2, thunkAPI) => {
    try {
      const data = await AuthService.oauthLogin(app.registrationId ,app.code); //  service 
      return { user: data };
    } catch (error: any) {
      const message =
        (error.response &&
          error.response.data &&
          error.response.data.message) ||
        error.message ||
        error.toString();
      return thunkAPI.rejectWithValue("oauthLogin   : " + message);
    }
  }
);

// Todo :   ( )
export const logout = createAsyncThunk(
  "auth/logout", //    
  async () => {
    await AuthService.logout(); //  service 
  }
);

/* ------    --------*/
// Todo : State( )  
const initialState: IAuth = user
  ? { isLoggedIn: true, user } // user   initialState = { isLoggedIn: true, user }
  : { isLoggedIn: false, user: null }; // user   initialState = { isLoggedIn: false, user: null }

// Todo :    ( ) :    
// name          :  
// state         : state( )
// reducers      :      :     
// extraReducers :    :    
// Todo :  : extraReducers : (builder) => {builder.addCase ( .fulfilled) , } :     
// Todo :  : extraReducers : (builder) => {builder.addCase ( .rejected) , }  :     
// Todo : state , action  ()   ,   
const authSlice = createSlice({
  name: "auth",
  initialState,
  //   
  reducers: {},
  //     : register, login, logout    
  extraReducers: (builder) => {
    builder
      // register  
      .addCase(register.fulfilled, (state) => {
        state.isLoggedIn = false;
      })
      // register  
      .addCase(register.rejected, (state) => {
        state.isLoggedIn = false;
      })
      // login  
      .addCase(login.fulfilled, (state, action) => {
        state.isLoggedIn = true;
        state.user = action.payload.user; // action.payload : user  
      })
      // login  
      .addCase(login.rejected, (state, action) => {
        state.isLoggedIn = false;
        state.user = null;
      })
      // logout  
      .addCase(logout.fulfilled, (state) => {
        state.isLoggedIn = false;
        state.user = null;
      })
      // oauthLogin  
      .addCase(oauthLogin.rejected, (state, action) => {
        state.isLoggedIn = false;
        state.user = null;
      })
      // oauthLogin  
      .addCase(oauthLogin.fulfilled, (state) => {
        state.isLoggedIn = false;
        state.user = null;
      });
  },
});

export default authSlice.reducer;
