import React, { useEffect, useState } from "react";
import ISteamList from "../../../types/steam/ISteamList";
import ISteam from "../../../types/steam/ISteam";
import TestOpenApiService from "../../../services/steam/SteamOpenApiService";
import Pagination from "@mui/material/Pagination";
import IProduct from "../../../types/IProduct";
import AdminProductService from "../../../services/admin/AdminProductService";
import designesis from "../../../assets/js/designesia";
import { Link } from "react-router-dom";
import ThumbNailService from "../../../services/product/ThumbNailService";
import { ToastContainer, toast } from "react-toastify";

function AdminControllPenal() {
  useEffect(() => {
    designesis();
  }, []);
  // TODO:  
  //  TODO : open api  
  let [steamList, setSteamList] = useState<Array<ISteamList>>([]);
  let [render, setRender] = useState<boolean>(false);
  let [steam, setSteam] = useState<ISteam>();
  // Todo : 
  let [discount, setDiscount] = useState<any>(0);
  //  
  const [searchName, setSearchName] = useState<string>("");

  // TODO :  () : page( ), count(  ) , pageSize(3,6,9  : 1  )
  const [page, setPage] = useState<number>(1); //           :  1
  const [count, setCount] = useState<number>(1); //           :  1
  const [pageSize, setPageSize] = useState<number>(10); // 1  :  20

  //    
  const onChangeSearchName = (e: React.ChangeEvent<HTMLInputElement>) => {
    const searchName = e.target.value;
    setSearchName(searchName);
  };

  // Todo : 
  const [selectedFiles, setSelectedFiles] = useState<FileList>();

  // TODO:  
  // TODO : Pagenation  
  // TODO :    -> page   
  const handlePageChange = (event: any, value: number) => {
    // value ==  
    setPage(value);
  };
  // Todo :  
  useEffect(() => {
    // todo :  
    reteiveSteamAll();
  }, [count]);

  // todo :  
  const reteiveSteamAll = () => {
    TestOpenApiService.findAll()
      .then((response: any) => {
        console.log(response);

        const { apps } = response.data.applist;

        // Todo :   
        setSteamList(apps.slice(50, 10000)); // name  appid

        // Todo :  
        setCount(Math.ceil(steamList.length / pageSize));

        setRender(true);
      })
      .catch((e: Error) => {
        console.log(e);
      });
  };

  // Todo : 
  const onClickButton = (appid: any) => {
    TestOpenApiService.findById(appid)
      .then((response: any) => {
        let data = response.data[`${appid}`].data;
        console.log(response.data);
        setSteam(data);
        setDiscount(0);
      })
      .catch((e: Error) => {
        console.log(e);
      });
  };

  // Todo :   
  const handleInputChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    if (discount >= 0 && discount <= 100) {
      setDiscount(event.target.value);
    }
  };

  //  Todo :  
  const onClickRegistration = (appid: number) => {
    if (selectedFiles?.[0] == null) {
      uploadProduct(appid, null);
    } else {
      upload(appid);
    }
  };

  const uploadProduct = (appid: number, uuid: any) => {
    // Todo :  
    let data: IProduct = {
      pid: appid,
      name: steam?.name,
      shortDescription: steam?.short_description,
      imgUrl: steam?.header_image,
      price: steam?.price_overview
        ? Math.ceil(((steam?.price_overview[`final`] / 100) * 1300) / 100) * 100
        : 0,
      finalPrice: steam?.price_overview
        ? Math.ceil(((steam?.price_overview[`final`] / 100) * 1300) / 100) *
          100 *
          ((100 - discount) / 100)
        : 0,
      tag: steam?.genres ? steam?.genres[0].description : "",
      discount: discount,
      uuid: uuid,
    };

    AdminProductService.create(data)
      .then((response: any) => {
        toastMessage(" .");
        setSelectedFiles(undefined);
        setDiscount(0);
      })
      .catch((e: Error) => {
        if ("Request failed with status code 400" == e.message) {
          toastMessage("    .");
        } else {
          toastMessage("  .  ");
        }
        console.log(e);
      });
  };

  const selectFile = (event: any) => {
    //      : event.target.files
    //  as  :     
    //                   (  )
    setSelectedFiles(event.target.files as FileList);
  };

  // Todo :   
  const upload = (appid: number) => {
    //    
    // ? = ,   null undefined 
    let currentFile = selectedFiles?.[0]; //   

    ThumbNailService.upload(appid, currentFile) //  
      .then((response: any) => {
        uploadProduct(appid, response.data);
        console.log(response.data);
      })
      .catch((e: Error) => {
        if ("Request failed with status code 400" == e.message) {
          toastMessage("    .");
        } else {
          toastMessage("  .  ");
        }
        console.log(e);
      });
  };

  const toastMessage = (message: string) => {
    toast.info(
      <div>
        <div>{message}</div>
        <div style={{ whiteSpace: "pre-line" }}>{steam?.name}</div>
      </div>,
      {
        position: "bottom-right",
        autoClose: 5000,
        hideProgressBar: true,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
      }
    );
  };

  return (
    <>
      {!render && (
        <>
          {/* <!-- page preloader begin --> */}
          <div id="de-loader"></div>
          {/* <!-- page preloader close --> */}
        </>
      )}
      <div className="no-bottom no-top" id="content">
        <div id="top"></div>
        {/* <!--  section  --> */}
        <section id="subheader" className="jarallax">
          <img
            src={require("../../../assets/images/background/bg-grid.webp")}
            className="jarallax-img"
            alt=""
          />
          <div className="container z-1000">
            <div className="row">
              <div className="col-lg-12">
                <div className="subtitle wow fadeInUp mb-3">Admin</div>
              </div>
              <div className="col-lg-6">
                <h2 className="wow fadeInUp mb20" data-wow-delay=".2s">
                    
                </h2>
              </div>
            </div>
          </div>
          {/*    */}
          <div className="row mt-5 justify-content-center">
            {/* w-50 :  , mx-auto : (margin: 0 auto), justify-content-center */}
            <div className="col-12 w-50 input-group mb-3" id="form_sb">
              <input
                type="text"
                className="form-control"
                placeholder="   "
                value={searchName}
                onChange={onChangeSearchName}
              />
              <div className="input-group-append">
                <button
                  className="btn btn-outline-primary overflow-hidden"
                  type="button"
                  style={{ height: "49px", width: "80px" }}
                >
                  Search
                </button>
              </div>
            </div>
          </div>
          {/*    */}
        </section>
        {/* <!--  section  --> */}
        <section className="no-top">
          <div className="container">
            <div className="row">
              <div className="col-lg-12 text-center wow fadeInUp">
                <div className="switch-set">
                  <div className="spacer-20"></div>
                </div>
              </div>
              {/*  (+)  */}
              <div className="col-md-12">
                <table className="table table-pricing dark-style text-center">
                  <thead>
                    <tr>
                      <th scope="col">ID</th>
                      <th scope="col" style={{ paddingLeft: "80px" }}>
                         
                      </th>
                      {/*     . */}
                      <th>   </th>
                      <th>  </th>
                    </tr>
                  </thead>
                  {/*  ()   */}
                  <tbody id="my-accordion">
                    {render &&
                      steamList
                        .filter((value) => value.name.indexOf(searchName) != -1)

                        .map((value, index) => (
                          <>
                            <tr key={index} style={{ height: "100px" }}>
                              <th>{value.appid}</th>
                              <td
                                className="d-spc"
                                style={{ width: "300px", paddingLeft: "80px" }}
                              >
                                {value.name}
                              </td>

                              {/*   */}
                              <td>
                                <Link
                                  to={`/control-panel/${value.appid}`}
                                  className="btn-main opt-1"
                                >
                                     
                                </Link>
                              </td>
                              <td
                                className="accordion-header"
                                id={"heading" + index}
                              >
                                <a
                                  data-bs-toggle="collapse"
                                  data-bs-target={"#collapse" + index}
                                  role="button"
                                  aria-expanded="false"
                                  aria-controls={"collapse" + index}
                                  className="btn-main opt-1"
                                  onClick={() => onClickButton(value.appid)}
                                >
                                    
                                </a>
                              </td>
                            </tr>
                            {/* steam details  */}
                            <tr>
                              <td
                                colSpan={5}
                                style={{ padding: "10px 0 10px 0" }}
                              >
                                {/*   */}
                                <div
                                  className="accordion-collapse collapse"
                                  id={"collapse" + index}
                                  aria-labelledby={"heading" + index}
                                  data-bs-parent="#my-accordion"
                                >
                                  <div className="my-card card-body">
                                    <table className="table table-pricing dark-style text-center">
                                      <thead>
                                        <tr style={{ height: "60px" }}>
                                          <td
                                            style={{
                                              padding: "0 0 0 0",
                                              verticalAlign: "middle",
                                            }}
                                          >
                                             
                                          </td>

                                          <td
                                            style={{
                                              padding: "0 0 0 0",
                                              verticalAlign: "middle",
                                            }}
                                          >
                                            {" "}
                                             {" "}
                                          </td>
                                          <td
                                            style={{
                                              padding: "0 0 0 0",
                                              verticalAlign: "middle",
                                            }}
                                          >
                                            {" "}
                                            {" "}
                                          </td>
                                          <td
                                            style={{
                                              padding: "0 0 0 0",
                                              verticalAlign: "middle",
                                            }}
                                          >
                                            {" "}
                                            
                                          </td>
                                          <td
                                            style={{
                                              padding: "0 30px 0 0",
                                              verticalAlign: "middle",
                                            }}
                                          >
                                            {" "}
                                            {" "}
                                          </td>
                                          <td
                                            style={{
                                              padding: "0 0 0 0",
                                              verticalAlign: "middle",
                                            }}
                                          >
                                            {" "}
                                             {" "}
                                          </td>
                                          <td
                                            style={{
                                              padding: "0 0 0 0",
                                              verticalAlign: "middle",
                                            }}
                                          >
                                            {" "}
                                            
                                          </td>
                                        </tr>
                                      </thead>
                                      <tbody>
                                        <tr style={{ height: "100px" }}>
                                          {/*   */}
                                          <td
                                            style={{
                                              padding: "10px 0 10px 0",
                                              verticalAlign: "middle",
                                            }}
                                          >
                                            <img
                                              src={
                                                steam?.header_image &&
                                                steam?.header_image
                                              }
                                              style={{
                                                width: "180px",
                                                borderRadius: "15px",
                                              }}
                                            ></img>
                                          </td>
                                          {/*   */}

                                          {/*    */}
                                          <td
                                            style={{
                                              width: "250px",
                                              padding: "10px 0 10px 0",
                                              verticalAlign: "middle",
                                            }}
                                          >
                                            {" "}
                                            {steam?.short_description &&
                                              steam?.short_description}
                                          </td>
                                          {/*    */}
                                          {/*   */}
                                          {steam?.price_overview ? (
                                            <td
                                              style={{
                                                padding: "10px 10px 10px 20px",
                                                verticalAlign: "middle",
                                              }}
                                            >
                                              {Math.ceil(
                                                ((steam?.price_overview[
                                                  `final`
                                                ] /
                                                  100) *
                                                  1300) /
                                                  100
                                              ) * 100}{" "}
                                              
                                            </td>
                                          ) : (
                                            <td
                                              style={{
                                                padding: "10px 10px 10px 20px",
                                                verticalAlign: "middle",
                                              }}
                                            >
                                               
                                            </td>
                                          )}
                                          {/*   */}
                                          {/*   */}
                                          <td
                                            style={{
                                              padding: "10px 20px 10px 20px",
                                              verticalAlign: "middle",
                                            }}
                                          >
                                            {" "}
                                            {steam?.genres &&
                                              steam?.genres[0].description}{" "}
                                          </td>
                                          {/*   */}
                                          {/*   */}
                                          <td
                                            style={{
                                              padding: "10px 0 10px 0",
                                              verticalAlign: "middle",
                                            }}
                                          >
                                            {steam?.price_overview &&
                                            steam.price_overview[`final`] >
                                              0 ? (
                                              <>
                                                {" "}
                                                <input
                                                  style={{
                                                    width: "70px",
                                                  }}
                                                  type="number"
                                                  id="discount"
                                                  required
                                                  className={`form-control ms-3`}
                                                  value={discount}
                                                  onChange={handleInputChange}
                                                  placeholder="unitPrice"
                                                  name="discount"
                                                />
                                              </>
                                            ) : (
                                              <>
                                                {" "}
                                                <input
                                                  style={{
                                                    width: "70px",
                                                  }}
                                                  type="number"
                                                  id="discount"
                                                  required
                                                  className={`form-control ms-3`}
                                                  value={discount}
                                                  onChange={handleInputChange}
                                                  placeholder="unitPrice"
                                                  disabled
                                                  name="discount"
                                                />
                                              </>
                                            )}
                                          </td>
                                          {/*   */}
                                          {/* upload / start */}
                                          <td
                                            style={{
                                              padding: "10px 0 10px 0",
                                              verticalAlign: "middle",
                                            }}
                                          >
                                            <div className="input-group">
                                              <input
                                                type="file"
                                                className="form-control"
                                                id="inputGroupFile02"
                                                style={{
                                                  width: "140px",
                                                  fontSize: "13px",
                                                }}
                                                onChange={selectFile}
                                              />
                                            </div>
                                          </td>
                                          {/* upload / end */}
                                          {/*   */}
                                          <td
                                            style={{
                                              padding: "10px 0 10px 0",
                                              verticalAlign: "middle",
                                            }}
                                          >
                                            <button
                                              className="btn-main opt-1"
                                              onClick={() =>
                                                onClickRegistration(value.appid)
                                              }
                                            >
                                               
                                            </button>
                                          </td>
                                          {/*   */}
                                        </tr>
                                      </tbody>
                                    </table>
                                  </div>
                                </div>
                              </td>
                            </tr>
                          </>
                        ))
                        .slice((page - 1) * pageSize, page * pageSize)}
                  </tbody>
                  {/*  ()   */}
                </table>
                <Pagination
                  className="pagination"
                  count={count}
                  page={page}
                  siblingCount={1}
                  boundaryCount={1}
                  shape="rounded"
                  onChange={handlePageChange}
                  sx={{
                    backgroundColor: "#1E1F22", //  
                    color: "white", //  
                    display: "flex",
                    justifyContent: "center",
                    marginTop: "20px", //   
                    "& .Mui-selected": {
                      backgroundColor: "white", //   
                      color: "#1E1F22", //   
                      "&:hover": {
                        backgroundColor: "white", //    
                      },
                    },
                    "& .MuiPaginationItem-root": {
                      fontSize: "1rem", //    
                      minWidth: "30px", //    
                      height: "30px", //   
                      color: "white", //     
                      "&:hover": {
                        backgroundColor: "black", //      
                      },
                      "&.Mui-selected": {
                        backgroundColor: "purple", //      
                        color: "white", //      
                        "&:hover": {
                          backgroundColor: "purple", //       
                        },
                      },
                    },
                  }}
                />
              </div>
              {/*  ()  */}
            </div>
          </div>
        </section>

        <section className="no-top">
          <div className="container">
            <div className="row g-4">
              <div className="col-lg-6">
                <div className="subtitle wow fadeInUp mb-3">
                  Incredibly features
                </div>
                <h2 className="wow fadeInUp mb20" data-wow-delay=".2s">
                  Premium Features
                </h2>
              </div>

              <div className="col-lg-6"></div>

              <div
                className="col-lg-3 col-sm-6 wow fadeInRight"
                data-wow-delay="0s"
              >
                <div>
                  <img
                    src={require("../../../assets/images/icons/1.png")}
                    className="mb20"
                    alt=""
                  />
                  <h4>Super Quick Setup</h4>
                  <p>
                    Dolor minim in pariatur in deserunt laboris eu pariatur
                    labore excepteur cupidatat cupidatat duis dolor in.
                  </p>
                </div>
              </div>

              <div
                className="col-lg-3 col-sm-6 wow fadeInRight"
                data-wow-delay=".2s"
              >
                <div>
                  <img
                    src={require("../../../assets/images/icons/2.png")}
                    className="mb20"
                    alt=""
                  />
                  <h4>Premium Hardware</h4>
                  <p>
                    Dolor minim in pariatur in deserunt laboris eu pariatur
                    labore excepteur cupidatat cupidatat duis dolor in.
                  </p>
                </div>
              </div>

              <div
                className="col-lg-3 col-sm-6 wow fadeInRight"
                data-wow-delay=".4s"
              >
                <div>
                  <img
                    src={require("../../../assets/images/icons/3.png")}
                    className="mb20"
                    alt=""
                  />
                  <h4>DDos Protection</h4>
                  <p>
                    Dolor minim in pariatur in deserunt laboris eu pariatur
                    labore excepteur cupidatat cupidatat duis dolor in.
                  </p>
                </div>
              </div>

              <div
                className="col-lg-3 col-sm-6 wow fadeInRight"
                data-wow-delay=".6s"
              >
                <div>
                  <img
                    src={require("../../../assets/images/icons/4.png")}
                    className="mb20"
                    alt=""
                  />
                  <h4>Fast Support</h4>
                  <p>
                    Dolor minim in pariatur in deserunt laboris eu pariatur
                    labore excepteur cupidatat cupidatat duis dolor in.
                  </p>
                </div>
              </div>
            </div>
          </div>
          {/* Toast Container */}
          <ToastContainer
            position="bottom-right"
            autoClose={5000}
            hideProgressBar
            newestOnTop={false}
            closeOnClick
            rtl={false}
            pauseOnFocusLoss
            draggable
            pauseOnHover
            style={{ width: "600px" }}
          />
        </section>
      </div>
    </>
  );
}

export default AdminControllPenal;
