import React, { useState } from "react";
import { useParams } from 'react-router-dom';
import { useEffect } from "react";
import { Formik, Form, Field, ErrorMessage } from "formik";
import * as Yup from "yup";
import emailjs from "emailjs-com";
import { useNavigate } from "react-router-dom";
import { useSelector } from "react-redux";
import { RootState, useAppDispatch } from "../../store/store";
import customMarquee from "../../assets/js/custom-marquee";
import AuthService from "../../services/auth/AuthService";
import designesis from "../../assets/js/designesia";
function ResetPassword() {
    const {email} = useParams();
    let navigate = useNavigate();

    useEffect(() => {
      customMarquee();
      designesis();
      // Todo : isLoggedIn (  true / false)
      // Todo : isLoggedIn == true 
      if (isLoggedIn) {
        navigate("/");
      }
    }, []);

    // Todo :   (state.) 
    // Todo :  : useSelector((state) => {state.})
    // Todo :       (isLoggedIn : true / false)
    const { isLoggedIn } = useSelector((state: RootState) => state.auth);
    const [message, setMessage] = useState("");
    const [seconds , setSeconds] = useState<number>(-1);

    useEffect(() => {
      const countdown = setInterval(() => {
        if (seconds > 0) {
          setSeconds(seconds - 1);
          setMessage(` . ${seconds}    .`)
        }
        if(seconds == 0) {
          navigate("/login");
        }
      }, 1000);
      return () => clearInterval(countdown);
    },[seconds]);
  
    // Todo :   lib
    // Todo : Formik   (initialValues) : html 
    // Todo :  (email , name) : Field 
    const initialValues = {
      password : "",
      repassword : ""
    };
  
    // Todo :  
    // Todo : Formit  : validationSchema
    // Todo : validationSchema :    
    // Todo : validationSchema = Yup.object().shape({ })
    const validationSchema = Yup.object().shape({
      // email   :
      password: Yup.string()
      .test(
        "len",
        "The password must be between 6 and 40 characters.",
        (val) => {
          if (
            val &&
            val.toString().length >= 6 &&
            val.toString().length <= 40
          ) {
            return true;
          }
          return false;
        }
      )
      .required("This field is required!"),
    // oneOf([Yup.ref("password")], "Password must match") :    
    repassword: Yup.string().oneOf(
      [Yup.ref("password")],
      "Passwords must match"
    ),
    });
  
    // Todo :   : submit(Formit)
    // Todo : Formit lib   email , name  
    const handleLogin = (formValue: any) => {
      const { password , repassword } = formValue;

      let data = {
        email : email,
        password : password
      }

      AuthService.resetPassword(data)
      .then((response : any) => {
        console.log(response);
        setSeconds(3);
        sendVerificationEmail();
      })
      .catch((e : Error )=> {
        console.log(e);
      })
    };
  
    const sendVerificationEmail = () => {
      //  
      //         ({{ }})   .
      const templateParams = {
        to_email: email,
        from_name: "playHost team",
        message: `   .
                        .
                      `,
      };
  
      emailjs
      .send(
        `${process.env.REACT_APP_EMAIL_JS_SERVICE_ID}`,
        "template_p4jxv0a",
        templateParams,
        `${process.env.REACT_APP_EMAIL_JS_API_KEY}`
      )
        .then((response: any) => {
          console.log("  :", response);
          // setIsEmailSent(true);
        })
        .catch((error: Error) => {
          console.error("  :", error);
          //      
        });
    };
  
    
  return (
    <>
      <div className="no-bottom no-top" id="content">
        <div id="top"></div>
        <section className="v-center jarallax">
          <div className="de-gradient-edge-top"></div>
          <div className="de-gradient-edge-bottom"></div>
          <img
            src={require("../../assets/images/background/2.webp")}
            className="jarallax-img"
            alt=""
          />
          <div className="container z-1000">
            <div className="row align-items-center">
              <div className="col-lg-4 offset-lg-4">
                <div className="padding40 rounded-10 shadow-soft bg-dark-1">
                  <div className="text-center">
                    <h4> </h4>
                  </div>
                  <div className="spacer-10"></div>
                  {/* Formik   */}
                  <Formik
                    initialValues={initialValues}
                    validationSchema={validationSchema}
                    onSubmit={handleLogin}
                  >
                    {/* Form   */}
                    {({ errors, touched }) => (
                      <Form id="form_register" className="form-border">
                        {/*    */}
                        <div className="col-md-12">
                            <div className="field-set">
                              <label> :</label>
                              <Field
                                type="password"
                                name="password"
                                id="password"
                                className={
                                  "form-control form-input" +
                                  (errors.password && touched.password
                                    ? " is-invalid"
                                    : "")
                                }
                              />
                              <ErrorMessage
                                name="password"
                                component="div"
                                className="invalid-feedback"
                              />
                            </div>
                          </div>
                          <div className="col-md-12">
                            <div className="field-set">
                              <label> :</label>
                              <Field
                                type="password"
                                name="repassword"
                                id="repassword"
                                className={
                                  "form-control" +
                                  (errors.repassword && touched.repassword
                                    ? " is-invalid"
                                    : "")
                                }
                              />
                              <ErrorMessage
                                name="repassword"
                                component="div"
                                className="invalid-feedback"
                              />
                            </div>
                          </div>
                        <div className="spacer-20"></div>
                        {/*   */}
                        <div id="submit">
                          <input
                            type="submit"
                            id="send_message"
                            value="Reset Password"
                            className="btn-main btn-fullwidth rounded-3"
                          />
                        </div>
                      </Form>
                    )}
                    {/* Form   */}
                  </Formik>
                  {/* Formik   */}
                </div>
                {message && (
                  <p className="alert alert-success mt-3 text-center">
                    {message}
                  </p>
                )}
              </div>
            </div>
          </div>
        </section>
      </div>
    </>
  )
}

export default ResetPassword