import React, { useEffect, useState } from "react";
import designesis from "../../assets/js/designesia";
import INews from "../../types/advanced/INews";
import FileDbService from "../../services/news/advanced/FileDbService";
import { useNavigate } from "react-router-dom";

function AddNews() {
  useEffect(() => {
    designesis();
  }, []);

  // todo:  
  // todo:  
  const initialNews = {
    uuid: null, // (    )
    fileTitle: "", // 
    fileContent: "", // 
    fileUrl: "", //   URL
    insertTime: "", // 
    updateTime: "" // 
  };

  // uploadFileDb 
  const [uploadFileDb, setUploadFileDb] = useState<INews>(initialNews);
  // todo  /   
  const [message, setMessage] = useState<String>("");
  //   todo:     
  const [selectedFiles, setSelectedFiles] = useState<FileList>();
  let navigate = useNavigate();

  // todo :  
  // todo: input   
  const handleInputChange = (event: any) => {
    const { name, value } = event.target; // 
    setUploadFileDb({ ...uploadFileDb, [name]: value }); // 
  };

  //  
  const upload = () => {
    //    
    // ? = ,   null undefined 
    let currentFile = selectedFiles?.[0]; //   

    FileDbService.upload(uploadFileDb, currentFile) //  
      .then((response: any) => {
        alert(" ");
        console.log(response.data);
        navigate("/news");
      })
      .catch((e: Error) => {
        console.log(e);
      });
  };

  // todo :      
  //   html  : <input type="file" />

  const selectFile = (event: any) => {
    //      : event.target.files
    //  as  :     
    //                   (  )
    setSelectedFiles(event.target.files as FileList);
  };

  return (
    <>
      <div className="no-bottom no-top" id="content">
        {/*   */}
        <div id="top"></div>
        {/* <!-- section begin --> */}
        <section id="subheader" className="jarallax">
          <img
            src={require("../../assets/images/background/subheader-news.webp")}
            className="jarallax-img"
            alt=""
          />
          <div className="container z-1000">
            <div className="row">
              <div className="col-lg-12">
                <div className="subtitle mb-3">News</div>
              </div>
              <div className="col-lg-6">
                <h2></h2>
                <br />
              </div>
            </div>
          </div>
        </section>
        {/*    */}

        {/*   */}
        <section>
          <div className="container">
            <div className="row">
              <div className="spacer-double"></div>

              <div className="col-lg-12">
                <div
                  className="padding40 rounded-10"
                  data-bgcolor="rgba(255, 255, 255, .1)"
                >
                  <h4> </h4>
                  <hr />

                  <div className="row align-items-center container">
                    <div
                      id="contact_form"
                      className="position-relative z1000 mb-3"
                    >
                      <div className="col-lg-12">
                        <div className="row g-3 align-items-center mb-3">
                          <div className="col-1">
                            <label htmlFor="fileTitle" className="form-label">
                              
                            </label>
                          </div>

                          <div className="col-10">
                            <input
                              type="text"
                              className="form-control"
                              id="fileTitle"
                              required
                              name="fileTitle"
                              value={uploadFileDb.fileTitle}
                              onChange={handleInputChange}
                            />
                          </div>
                        </div>
                      </div>
                      {/* <!--     --> */}
                      <div className="col-lg-12">
                        <div className="row g-3 align-items-center mb-3">
                          <div className="col-1">
                            <label htmlFor="fileContent" className="form-label">
                              
                            </label>
                          </div>

                          <div className="col-10 mb-3">
                            <textarea
                              name="fileContent"
                              id="fileContent"
                              className="form-control"
                              placeholder=""
                              value={uploadFileDb.fileContent}
                              onChange={handleInputChange}
                              required
                              style={{ height: "300px" }}
                            />
                          </div>
                        </div>
                      </div>
                      {/*       */}
                      {!selectedFiles && (
                        <div className="alert alert-warning wow fadeInUp" role="alert">
                            .
                        </div>
                      )}
                      {/* <!--     --> */}
                    </div>

                    <hr />
                    <h4> </h4>

                    <div className="input-group mb-3">
                      {/* upload / start */}
                      <input
                        type="file"
                        className="form-control mb-3"
                        id="inputGroupFile02"
                        onChange={selectFile}
                      />
                    </div>
                    {/* upload / end */}

                    <hr />

                    <button
                      className="btn btn-outline-warning col-3 container"
                      type="button"
                      id="inputGroupFileAddon04"
                      disabled={!selectedFiles}
                      onClick={upload}
                      style={{ width: "200px ", height: "38px" }}
                    >
                      
                    </button>

                    {/* upload /    */}
                    {message && (
                      <div className="alert alert-success" role="alert">
                        {message}
                      </div>
                    )}
                    {/* upload /    */}
                  </div>
                </div>
              </div>
            </div>
          </div>
        </section>
        {/*    */}
      </div>
    </>
  );
}

export default AddNews;
