import { useNavigate, useParams, useSearchParams } from "react-router-dom";
import emailjs from "emailjs-com";
import { useEffect, useState } from "react";
import designesis from "../../assets/js/designesia";
import {  useSelector } from "react-redux";
import { RootState } from "../../store/store";
import CartService from "../../services/cart/CartService";

import ILibrary from "../../types/ILibrary";
import IUser from "../../types/auth/IUser";
import ICartDto from "../../types/Dto/ICartDto";
import LibraryService from "../../services/library/LibraryService";
import UserService from "../../services/user/UserService";

import "react-toastify/dist/ReactToastify.css";
import { ToastContainer, toast } from "react-toastify";

//  
let blank = {
  height: "200px",
};

let head = {
  height: "200px",
};

let text = {
  height: "350px",
};

export function SuccessPage() {
  const { user } = useSelector((state: RootState) => state.auth);
  let [render, setRender] = useState<boolean>(false);
  const queryParameters = new URLSearchParams(window.location.search);
  let queryAmount: any = queryParameters.get("amount");
  const [price, setPrice] = useState<number>(0);

  useEffect(() => {
    retrieveCart();
    designesis();
  }, []);

  useEffect(() => {
    if (render) success();
  }, [render]);

  const [cart, setCart] = useState<Array<ICartDto>>([]);
  const [userDto, setUserDto] = useState<IUser>();
  let navigate = useNavigate();

  //  
  const priceSum = (cart: any) => {
    let price = 0;
    for (let index = 0; index < cart.length; index++) {
      price += cart[index].finalPrice;
    }
    setPrice(price);
  };

  //   
  const retrieveCart = () => {
    CartService.getAll(user?.userId, 0, 100)
      .then((response: any) => {
        console.log(response);
        const { cart, userDto } = response.data;
        setCart(cart);
        setUserDto(userDto);
        priceSum(cart);
        setRender(true);
      })
      .catch((e: Error) => {
        console.log(e);
      });
  };

  const success = () => {
    console.log("price ", price);
    // cart  
    if (cart && userDto && cart.length > 0) {
      // Todo :   

      let point =
        (userDto.point as number) - (price - queryAmount) + price / 100;
      console.log("userDto.point", userDto.point);
      console.log("price - queryAmount", price - queryAmount);

      UserService.updatePoint(user?.userId, point)
        .then((response: any) => {
          console.log(response);
        })
        .catch((e: Error) => {
          console.log(e);
        });

      // Todo : Cart  Library 
      cart.map((value, index) =>
        CartService.updateCompleteYn(value.cid)
          .then((response: any) => {
            console.log(response.data);

            let data: ILibrary = {
              lid: null,
              userId: value.userId,
              pid: value.pid,
              finalPrice: value.finalPrice,
              insertTime: "",
              requestRefund: null,
              refundReason: "",
              refund: null,
              refundTime: null,
            };

            LibraryService.create(data)
              .then((response: any) => {
                console.log(response);
              })
              .catch((e: Error) => {
                console.log(e);
              });
          })
          .catch((e: Error) => {
            console.log(e);
            navigate("/checkout");
          })
      );
      sendVerificationEmail();
    }
  };

  // Todo :  
  // const [isEmailSent, setIsEmailSent] = useState(false);
  const sendVerificationEmail = () => {
    //  
    //         ({{ }})   .
    const templateParams = {
      to_email: userDto?.email,
      to_name: userDto?.name,
      from_name: "",
      message: ` playHost  .
                   .

                    
                    ${cart.map(
                      (value) =>
                        "  : " +
                        value.pid +
                        "\n  : " +
                        value.name +
                        "\n  : " +
                        value.finalPrice +
                        "\n\n"
                    )}
                    
                      : ${price} 

                     .
                    `,
    };

    emailjs
      .send(
        `${process.env.REACT_APP_EMAIL_JS_SERVICE_ID}`,
        "template_p4jxv0a",
        templateParams,
        `${process.env.REACT_APP_EMAIL_JS_API_KEY}`
      )
      .then((response: any) => {
        console.log("  :", response);
        // setIsEmailSent(true);
        //      
      })
      .catch((error: Error) => {
        console.error("  :", error);
        //      
      });

    //   
    toast.success(
      "   .     .."
    );
  };

  return (
    <>
      <div className="no-bottom no-top" id="content">
        <div id="top"></div>
        {/* <!-- section begin --> */}
        <section id="subheader" className="jarallax">
          <div className="de-gradient-edge-bottom"></div>
          <img
            src={require("../../assets/images/background/subheader-game.webp")}
            className="jarallax-img"
            alt=""
          />
          <div className="container">
            <div className="row gx-5 align-items-center">
              <div style={blank}></div>
              <h2
                className="text-center wow fadeInUp"
                data-wow-delay=".2s"
                style={head}
              >
                
              </h2>

              <h4 style={text} className="text-center wow fadeInUp">
                  
              </h4>
              <div className="swiper-wrapper">
                <div className="col-lg-12 offset-lg-5">
                  <a className="btn-main mb-5" href="/">
                     
                  </a>
                </div>
              </div>
            </div>
          </div>
        </section>

        {/* <!-- section close --> */}
      </div>
      <ToastContainer
        style={{ width: "400px" }} // Set your preferred width here
        position="bottom-center" // Set the position to bottom-center
        autoClose={5000}
        hideProgressBar={false}
        newestOnTop={false}
        closeOnClick
        rtl={false}
        pauseOnFocusLoss
        draggable
        pauseOnHover
      />
    </>
  );
}
