import React from "react";
import { useEffect } from "react";

import { Formik, Form, Field, ErrorMessage } from "formik";
import * as Yup from "yup";
import { Link, useNavigate } from "react-router-dom";
import { useSelector } from "react-redux";
import { RootState, useAppDispatch } from "../../store/store";
import { login } from "../../store/slices/auth";
import customMarquee from "../../assets/js/custom-marquee";
import IUser from "../../types/auth/IUser";
import designesis from "../../assets/js/designesia";


function Login() {
  useEffect(() => {
    customMarquee();
    designesis();
    console.log("kakaoClientId",kakaoClientId);
    console.log("kakaoRedirectUrl",kakaoRedirectUrl);
    console.log("googleClientId",googleClientId);
    console.log("googleRedirectUrl",googleRedirectUrl);

    // Todo : isLoggedIn (  true / false)
    // Todo : isLoggedIn == true 
    if (isLoggedIn) {
      navigate("/");
    }
  });

  let navigate = useNavigate();

  // Todo :   (state.) 
  // Todo :  : useSelector((state) => {state.})
  // Todo :       (isLoggedIn : true / false)
  const { isLoggedIn } = useSelector((state: RootState) => state.auth);
  const kakaoClientId = process.env.REACT_APP_KAKAO_CLIENT_ID;
  const kakaoRedirectUrl = process.env.REACT_APP_KAKAO_REDIRECT_URL;  
  const googleClientId = process.env.REACT_APP_GOOGLE_CLIENT_ID;
  const googleRedirectUrl = process.env.REACT_APP_GOOGLE_REDIRECT_URL;

  // Todo :   
  // Todo :     : useAppDispatch()
  // Todo :   : dispatch()
  // Todo :   : dispatch(login) , dispatch(logout)
  const dispatch = useAppDispatch();

  // Todo :   lib
  // Todo : Formik   (initialValues) : html 
  // Todo :  (email , password) : Field 
  const initialValues = {
    email: "",
    password: "",
  };

  // Todo :  
  // Todo : Formit  : validationSchema
  // Todo : validationSchema :    
  // Todo : validationSchema = Yup.object().shape({ })
  const validationSchema = Yup.object().shape({
    // email , password   :
    // string() :  ?
    // required() => 
    email: Yup.string().required("email   ."),
    password: Yup.string().required("password   ."),
  });

  // Todo :   : submit(Formit)
  // Todo : Formit lib   email , password  
  const handleLogin = (formValue: any) => {
    const { email, password } = formValue;

    const data: IUser = {
      email, // == email : email ( )
      password, // == password : password
      name: "", //  
      description : "",
      insertTime :"",
      role: null,
      point : null
    };

    // Todo :    
    dispatch(login(data))
      // Todo : .unwrap() : redux     
      .unwrap()
      .then(() => {
        alert(" .");
        navigate("/");
        window.location.reload(); //  
      })
      .catch((e: Error) => {
        console.log(e);
        alert("    .");
      });
  };


  return (
    <>
      <div className="no-bottom no-top" id="content">
        <div id="top"></div>
        <section className="v-center jarallax">
          <div className="de-gradient-edge-top"></div>
          <div className="de-gradient-edge-bottom"></div>
          <img
            src={require("../../assets/images/background/2.webp")}
            className="jarallax-img"
            alt=""
          />
          <div className="container z-1000">
            <div className="row align-items-center">
              <div className="col-lg-4 offset-lg-4">
                <div className="padding40 rounded-10 shadow-soft bg-dark-1">
                  <div className="text-center">
                    <h4>  !</h4>
                  </div>
                  <div className="spacer-10"></div>
                  {/* Formik   */}
                  <Formik
                    initialValues={initialValues}
                    validationSchema={validationSchema}
                    onSubmit={handleLogin}
                  >
                    {/* Form   */}
                    {({ errors , touched }) => (
                      <Form id="form_register" className="form-border">
                        {/*    */}
                        <div className="field-set">
                          <label></label>
                          <Field
                            type="text"
                            name="email"
                            id="email"
                            className={
                              "form-control" +
                              (errors.email && touched.email
                                ? " is-invalid"
                                : "")
                            }
                          />
                          <ErrorMessage
                            name="email"
                            component="div"
                            className="invalid-feedback"
                          />
                        </div>
                        {/*   */}
                        <div className="field-set">
                          <label></label>
                          <Field
                            type="password"
                            name="password"
                            id="password"
                            className={
                              "form-control form-input" +
                              (errors.password && touched.password
                                ? " is-invalid"
                                : "")
                            }
                          />
                          <ErrorMessage
                            name="password"
                            component="div"
                            className="invalid-feedback"
                          />
                        </div>
                        <div className="field-set">
   
                          <br />
                        </div>
                        <div className="spacer-20"></div>
                        {/*   */}
                        <div id="submit">
                          <input
                            type="submit"
                            id="send_message"
                            value="Sign In"
                            className="btn-main btn-fullwidth rounded-3"
                          />
                        </div>
                      </Form>
                    )}
                    {/* Form   */}
                  </Formik>
                  {/* Formik   */}

                  {/*      */}
                  <div className="title-line">
                    forgot&nbsp;email&nbsp;or&nbsp;password&nbsp;?
                  </div>
                  <div className="row g-2">
                    <div className="col-lg-6">
                      <a className="btn-sc btn-fullwidth mb10" href="/forgot-password">
                        <img
                          src={require("../../assets/images/svg/google_icon.svg")}
                          alt=""
                        />
                         
                      </a>
                    </div>
                    <div className="col-lg-6">
                      <a className="btn-sc btn-fullwidth mb10" href="/register">
                        <img
                          src={require("../../assets/images/svg/google_icon.svg")}
                          alt=""
                        />
                        
                      </a>
                    </div>
                  </div>
                  {/* google  facebook  */}
                  <div className="title-line">
                    Or&nbsp;login&nbsp;up&nbsp;with
                  </div>
                  <div className="row g-2">
                    <div className="col-lg-6">
                      <Link className="btn-sc btn-fullwidth mb10" to={`https://accounts.google.com/o/oauth2/auth/oauthchooseaccount?client_id=${googleClientId}&redirect_uri=${googleRedirectUrl}&response_type=code&scope=https%3A%2F%2Fwww.googleapis.com%2Fauth%2Fuserinfo.email%20https%3A%2F%2Fwww.googleapis.com%2Fauth%2Fuserinfo.profile&serviso&o2v=1&theme=glif&flowName=GeneralOAuthFlow`}>
                        <img
                          src={require("../../assets/images/svg/google_icon.svg")}
                          alt=""
                        />
                        Google
                      </Link>
                    </div>
                    <div className="col-lg-6">
                      <Link className="btn-sc btn-fullwidth" to={`https://kauth.kakao.com/oauth/authorize?client_id=${kakaoClientId}&redirect_uri=${kakaoRedirectUrl}&response_type=code`}>
                        <img
                          src={require("../../assets/images/svg/google_icon.svg")}
                          alt=""
                        />
                        Kakao
                      </Link>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </section>
      </div>
    </>
  );
}

export default Login;
