import { useCallback, useEffect, useRef, useState } from "react";
import {
  PaymentWidgetInstance,
  loadPaymentWidget,
  ANONYMOUS,
} from "@tosspayments/payment-widget-sdk";

//    (unique string ID generator)
import { nanoid } from "nanoid";
import ICart from "../../types/ICart";
import CartService from "../../services/cart/CartService";
import { useSelector } from "react-redux";
import { RootState } from "../../store/store";
import designesis from "../../assets/js/designesia";
import { Link, useNavigate } from "react-router-dom";
import UserLibraryService from "../../services/user/UserLibraryService";
import IUser from "../../types/auth/IUser";
import ICartDto from "../../types/Dto/ICartDto";

const clientKey = `${process.env.REACT_APP_TOSS_PAYMENTS_CLIENT_KEY}`; //     
const customerKey = `${process.env.REACT_APP_TOSS_PAYMENTS_CUSTOMER_KEY}`; //         ID

function CheckOut() {
  const [render, setRender] = useState<Boolean>(false);
  const [price, setPrice] = useState<number>(0);
  useEffect(() => {
    designesis();

    (async () => {
      // ------    ------
      //   customerKey  ANONYMOUS .
      const paymentWidget = await loadPaymentWidget(clientKey, customerKey); //  
      // const paymentWidget = await loadPaymentWidget(clientKey, ANONYMOUS)  //  

      // ------   UI  ------
      //  UI   . `#payment-method`  CSS     .
      // DOM     .
      // https://docs.tosspayments.com/reference/widget-sdk#renderpaymentmethods---
      const paymentMethodsWidget = paymentWidget.renderPaymentMethods(
        "#payment-widget",
        { value: price },
        //    UI variantKey
        //  variantKey    .  UI     .
        // https://docs.tosspayments.com/guides/payment-widget/admin#--ui
        { variantKey: "DEFAULT" }
      );

      // ------   UI  ------
      //  UI   . `#agreement`  CSS  .
      // https://docs.tosspayments.com/reference/widget-sdk#renderagreement-
      paymentWidget.renderAgreement(
        "#agreement",
        { variantKey: "AGREEMENT" } //   UI 
      );
      paymentWidgetRef.current = paymentWidget;
      paymentMethodsWidgetRef.current = paymentMethodsWidget;
    })();
  }, [render]);

  const paymentWidgetRef = useRef<PaymentWidgetInstance | null>(null);
  const paymentMethodsWidgetRef = useRef<ReturnType<
    PaymentWidgetInstance["renderPaymentMethods"]
  > | null>(null);

  // Todo :   (state.) 
  // Todo :  : useSelector((state) => {state.})
  // Todo :       (isLoggedIn : true / false)
  const { user } = useSelector((state: RootState) => state.auth);

  const userName = useSelector((state: RootState) => state.auth.user?.name);
  // 
  // cart 
  const [cart, setCart] = useState<Array<ICartDto>>([]);
  const [userDto, setUserDto] = useState<IUser>();

  const [point, setPoint] = useState<number>(0);

  // 
  const [page, setPage] = useState(1);
  const [pageSize, setPageSize] = useState(50);
  const [count, setCount] = useState(1);

  //  

  const retrieveCart = () => {
    CartService.getAll(user?.userId, page - 1, pageSize)
      .then((response: any) => {
        console.log(response);
        const { cart, priceSum, totalPages, userDto } = response.data;
        setCart(cart);
        setUserDto(userDto);
        setPrice(priceSum);
        setCount(totalPages);
        setRender(true);
      })
      .catch((e: Error) => {
        console.log(e);
      });
  };

  //  
  const priceSum = () => {
    const changePrice = price - point;
    if (changePrice === 0) {
      setPrice(1);
    } else {
      setPrice(changePrice);
    }
  };

  useEffect(() => {
    retrieveCart();
  }, [page, pageSize]);

  const handleChange = (event: any) => {
    setPoint(event.target.value);
  };

  useEffect(() => {
    const paymentMethodsWidget = paymentMethodsWidgetRef.current;

    if (paymentMethodsWidget == null) {
      return;
    }

    // ------   ------
    //    .
    // https://docs.tosspayments.com/reference/widget-sdk#updateamount-
    paymentMethodsWidget.updateAmount(price);
  }, [render, price]);

  return (
    <>
      <div className="no-bottom no-top" id="content">
        <div id="top"></div>

        {/* <!-- section begin --> */}
        <section id="subheader" className="jarallax">
          <div className="de-gradient-edge-bottom"></div>
          <img
            src={require("../../assets/images/background/subheader-game.webp")}
            className="jarallax-img"
            alt=""
          />
          <h2
            className="container text-center wow fadeInUp mb20 mt-5"
            data-wow-delay=".2s"
          >
            
          </h2>
        </section>
        {/* <!-- section close --> */}

        <div className="container">
          <br />
          <h3> {userName} </h3>
          <hr />
          <div>
            {cart &&
              cart.map((data) => (
                <div className="card mb-2 bg-dark">
                  <div className="card-body m-2">
                    <h5 className="card-title">{data.name}</h5>
                    <p className="card-text">
                      <span>
                        {data.discount !== 0 && (
                          <>
                            <del>{data.price} </del> {data.discount}% {" "}
                            <br />
                          </>
                        )}
                        {` : ${data.finalPrice}`}
                      </span>
                    </p>
                  </div>
                </div>
              ))}
            <h3 className="mt-5 mb-5">
                : {cart && price.toLocaleString()}
            </h3>
            <h5 className="coupon-title"> </h5>
            <div>
              {/*    price   */}
              <label>
                {userDto?.point && price < userDto?.point && (
                  <input
                    type="range"
                    onChange={handleChange}
                    style={{ width: "140px" }}
                    value={point}
                    max={price}
                    min="0"
                    step="10"
                  />
                )}
                {userDto?.point && price > userDto?.point && (
                  <input
                    type="range"
                    onChange={handleChange}
                    style={{ width: "140px" }}
                    value={point}
                    max={userDto.point}
                    min="0"
                    step="10"
                  />
                )}
                &nbsp;&nbsp;&nbsp; | &nbsp; {userDto?.point}   {" "}
                {point}  
              </label>
              <br />
            </div>
            <hr />

            <div id="payment-widget" />
            <div id="agreement" />
            <div className="text-center">
              <button
                className="btn-main mt-5 mb-5 "
                onClick={async () => {
                  const paymentWidget = paymentWidgetRef.current;

                  try {
                    priceSum();

                    // ------ ''     ------
                    //       SDK .
                    // https://docs.tosspayments.com/reference/widget-sdk#requestpayment-

                    await paymentWidget?.requestPayment({
                      orderId: nanoid(), //   ID
                      orderName: "Game product", //    
                      customerName: `${userDto?.name}`,  //   
                      customerEmail: `${userDto?.email}`, //    
                      successUrl: `${window.location.origin}/success`, //      url
                      failUrl: `${window.location.origin}/fail`, //      url
                    });
                  } catch (error) {
                    //  
                    console.error(error);
                  }
                }}
              >
                <Link to="#" onClick={priceSum} style={{ color: "#FFFFFF" }}>
                  {` `}
                </Link>
              </button>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}

export default CheckOut;
